import React from 'react';
import { CSSTransitionGroup } from 'react-transition-group';
import PropTypes from 'prop-types';
import FlexWrapper from 'components/FlexWrapper';
import ListGroup from 'components/ListGroup';

import { calculateDayMacros } from './dispatch';
import FoodListItem from './FoodListItem';

const Col = ({ num, children }) => <div>{children}: {num}</div>;

Col.propTypes = {
  num: PropTypes.number,
  children: PropTypes.node
};

const FoodList = ({ foods }) => {
  const { raw } = calculateDayMacros(foods);

  const macroSummary = (
    <FlexWrapper>
      <Col num={raw.calories}>Calories</Col>
      <Col num={raw.protein}>Protein</Col>
      <Col num={raw.carbs}>Carbs</Col>
      <Col num={raw.fat}>Fat</Col>
    </FlexWrapper>
  );

  if (!foods.length) return macroSummary;

  return (
    <div>
      {macroSummary}
      <CSSTransitionGroup
        transitionName="fade"
        transitionLeaveTimeout={100}
        transitionEnterTimeout={100}
      >
        <ListGroup>
          {foods.map(food => (
            <FoodListItem key={`${food.id}_${food.confirmed}`} food={food} />)
          )}
        </ListGroup>
      </CSSTransitionGroup>
    </div>
  );
};

FoodList.propTypes = {
  foods: PropTypes.array
};

export default FoodList;



