import React from 'react';
import PropTypes from 'prop-types';
import Button from 'components/Button';
import FlexWrapper from 'components/FlexWrapper';
import { Input } from 'components/FormComponents';
import OptionGroup from 'components/OptionGroup';

const MeasurementForm = ({ change, submitForm, units, age, gender, weight, height, btnTxt }) => (
  <form onSubmit={submitForm}>
    <div className="form-group">
      <div>Units</div>
      <FlexWrapper>
        <label htmlFor="unitsImperial" className="col-xs-6 text-center">
          <Input
            id="unitsImperial"
            checked={units === 'imperial'}
            name="units"
            onChange={ev => change(ev, 'units')}
            value="imperial"
            type="radio"
          />
          U.S. Customary
        </label>
        <label htmlFor="unitsMetric" className="col-xs-6 text-center">
          <Input
            id="units"
            checked={units === 'metric'}
            name="unitsMetric"
            onChange={ev => change(ev, 'units')}
            type="radio"
            value="metric"
          />
          Metric
        </label>
      </FlexWrapper>

      <label htmlFor="dob">Date of Birth:</label>
      <Input className="form-control" id="dob" value={age} onChange={ev => change(ev, 'age')} type="date" />

      <label htmlFor="gender">Gender:</label>
      <OptionGroup
        value={gender}
        onChange={ev => change(ev, 'gender')}
        id="gender"
        options={['Male', 'Female']}
      />
      <label htmlFor="weight">Weight ({units === 'imperial' ? 'lbs' : 'kg'}):</label>
      <Input
        id="weight"
        value={weight}
        onChange={ev => change(ev, 'weight')}
        type="number"
      />

      <label htmlFor="height">Height ({units === 'imperial' ? 'in' : 'cm'}):</label>
      <Input
        id="height"
        value={height}
        onChange={ev => change(ev, 'height')}
        type="number"
      />
    </div>
    <div className="form-group">
      <Button primary>{btnTxt}</Button>
    </div>
  </form>
);

MeasurementForm.propTypes = {
  change: PropTypes.func,
  submitForm: PropTypes.func,
  units: PropTypes.string,
  age: PropTypes.string,
  gender: PropTypes.string,
  weight: PropTypes.string,
  height: PropTypes.string,
  btnTxt: PropTypes.string
};

export default MeasurementForm;



