import styled from 'styled-components';
import { fixed } from 'colorCSS';
import color from 'color';

export const Input = styled.input`
  display: inline-block;
  ${props => (props.inline ? null : 'width: 100%;')}
  height: ${props => ((props.small || props.sm) ? '30px' : '34px')};
  padding: ${props => ((props.small || props.sm) ? '5px 10px' : '6px 12px')};
  font-size: ${props => ((props.small || props.sm) ? '12px' : '14px')};
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ccc;
  ${(props) => {
    if (props.danger) {
      return `
        background-color: ${color('#f00').lighten(0.9).string()};
        border-color: ${color('#f00').lighten(0.7).string()};
        color: #f00;
      `;
    }
    if (props.success) {
      return `
        background-color: rgba(0, 255, 0, 0.1);
        border-color: ${color('#0f0').lighten(0.7).string()};
        color: #0f0;
      `;
    }
    return null;
  }}
`;

export const Select = styled.select`
  display: inline-block;
  ${props => (props.inline ? null : 'width: 100%;')}
  height: ${props => ((props.small || props.sm) ? '30px' : '34px')};
  padding: ${props => ((props.small || props.sm) ? '5px 10px' : '6px 12px')};
  font-size: ${props => ((props.small || props.sm) ? '12px' : '14px')};
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ccc;
  ${fixed}
`;

export const Label = styled.label`
  color: gray;
  font-size: 0.8em;
`;



