import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Input } from 'components/FormComponents';

const SearchForm = ({ onSearchSubmit, searchVal, changeSearchTerm }) => (
  <form onSubmit={onSearchSubmit} className="form-group">
    <Input
      type="text"
      placeholder="Search Food"
      value={searchVal}
      onChange={changeSearchTerm}
    />
  </form>
);

SearchForm.propTypes = {
  onSearchSubmit: PropTypes.func,
  searchVal: PropTypes.string,
  changeSearchTerm: PropTypes.func
};

const mapStateToProps = ({ mealplanner }) => ({ retainedFoods: mealplanner.retainedFoods });

export default connect(mapStateToProps)(SearchForm);



