import React from 'react';
import PropTypes from 'prop-types';
import { Input } from 'components/FormComponents';
import FlexWrapper from 'components/FlexWrapper';

const GoalForm = ({ proteinGoal, carbGoal, fatGoal, updateGoal, calculateFood }) => (
  <div style={{ display: 'inline' }}>
    <FlexWrapper>
      <div>
        <label htmlFor="proteinGoal">Protein Goal:</label>
        <Input
          id="proteinGoal"
          value={proteinGoal}
          className="form-control"
          type="number"
          onChange={ev => updateGoal(ev, 'proteinGoal')}
        />
      </div>
      <div>
        <label htmlFor="carbGoal">Carb Goal:</label>
        <Input
          id="carbGoal"
          value={carbGoal}
          className="form-control"
          type="number"
          onChange={ev => updateGoal(ev, 'carbGoal')}
        />
      </div>
      <div>
        <label htmlFor="fatGoal">Fat Goal:</label>
        <Input
          id="fatGoal"
          value={fatGoal}
          className="form-control"
          type="number"
          onChange={ev => updateGoal(ev, 'fatGoal')}
        />
      </div>
    </FlexWrapper>
    <button className="btn btn-primary" onClick={calculateFood}>Calculate</button>
  </div>
);

GoalForm.propTypes = {
  proteinGoal: PropTypes.number,
  carbGoal: PropTypes.number,
  fatGoal: PropTypes.number,
  updateGoal: PropTypes.func,
  calculateFood: PropTypes.func
};

export default GoalForm;



