import React from 'react';
import { DateField } from 'react-date-picker';
import PropTypes from 'prop-types';

import { connect } from 'react-redux';
import styled from 'styled-components';
import Button from 'components/Button';
import FlexWrapperBase from 'components/FlexWrapper';
import { changeDay, nextDayClick, previousDayClick } from './dispatch';

const FlexWrapper = FlexWrapperBase.extend`
  justify-content: center;
  align-items: stretch;
  &>*:first-child ${Button} {
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-right: none;
  }
  &>*:last-child ${Button} {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-left: none;
  }
`;

const DField = styled(DateField)`
  border: 1px solid #ccc;
  & input {
    max-width: 100px;
  }
`;

const Wrapper = styled.div`
  margin: auto;
  text-align: center;
`;

class ChangeDayContainer extends React.Component {
  constructor() {
    super();
    this.changeDate = this.changeDate.bind(this);
  }

  /**
   * Update the date for the entire application
   * @param {string} date
   */
  changeDate(date) {
    this.props.changeDay(new Date(new Date(date).getTime() + 86400000));
  }

  render() {
    const { date, prevDayClick, nexDayClick, displayDay } = this.props;

    const currentDate = new Date();
    const isToday = date.toDateString() === currentDate.toDateString();

    const dateDiff = Math.round((date - currentDate) / 86400000);

    return (
      <Wrapper>
        <div>
          <FlexWrapper>
            <div>
              <Button onClick={() => prevDayClick(date)}>
                &minus;
              </Button>
              <div>
                <small>{dateDiff < 0 && `(${dateDiff})`}</small>
              </div>
            </div>
            <div>
              <DField
                dateFormat="YYYY-MM-DD"
                defaultValue={date}
                onChange={this.changeDate}
              />
              <div>
                <small>
                  <i>YYYY-MM-DD</i>
                </small>
              </div>
            </div>
            <div>
              <Button onClick={() => nexDayClick(date)}>
                +
              </Button>
              <div>
                <small>{dateDiff > 0 && `(${dateDiff})`}</small>
              </div>
            </div>
          </FlexWrapper>
          <div>
            {displayDay &&
              <b>{isToday
                ? <span className="text-primary">Today</span>
                : <span className="text-warning">{date.toDateString()}</span>
              }</b>
            }
          </div>
        </div>
      </Wrapper>
    );
  }
}

ChangeDayContainer.propTypes = {
  changeDay: PropTypes.func,
  nexDayClick: PropTypes.func,
  prevDayClick: PropTypes.func,
  date: PropTypes.object,
  displayDay: PropTypes.bool
};

ChangeDayContainer.defaultProps = {
  date: new Date()
};

const mapDispatchToProps = dispatch => ({
  changeDay: dt => dispatch(changeDay(dt)),
  nexDayClick: dt => dispatch(nextDayClick(dt)),
  prevDayClick: dt => dispatch(previousDayClick(dt))
});

const mapStateToProps = ({ root }) => ({ date: root.date });

export default connect(mapStateToProps, mapDispatchToProps)(ChangeDayContainer);



