import api from 'utils/api';
import { RECEIVE_LIST_ITEMS } from 'app/redux/constants';

/**
 * Get the shopping list
 * @param {string} date
 */
export const getList = date => dispatch => api
  .get('/api/shopping-list/list', {
    params: {
      date
    }
  })
  .then(({ data }) => dispatch({
    type: RECEIVE_LIST_ITEMS,
    payload: data
  }));



