import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { CSSTransitionGroup } from 'react-transition-group';
import HeightCollapse from 'components/HeightCollapse';
import Button from 'components/Button';
import FlexWrapper from 'components/FlexWrapper';
import Label from 'components/Label';
import { destroyFoodRecord, confirmRecord } from './dispatch';

import ModifyRecordForm from './containers/ModifyRecordForm';

const Column = FlexWrapper.extend`
  flex-direction: column;
  height: ${props => props.height};
  align-items: flex-end;
`;

class FoodListItem extends React.Component {
  constructor() {
    super();
    this.state = {
      updating: false,
      showingQuants: false
    };

    this.changeUpdating = this.changeUpdating.bind(this);
    this.toggleQuants = this.toggleQuants.bind(this);
  }

  changeUpdating() {
    this.setState({ updating: !this.state.updating });
  }

  toggleQuants() {
    this.setState({ showingQuants: !this.state.showingQuants });
  }

  render() {
    const { destroyRecord, confirmFoodRecord, food, date } = this.props;
    const { showingQuants } = this.state;
    const allQuants = getQuantities(food);

    const { id, confirmed, Main, Sub, Quantity, Unit } = food;

    const confirmBtn = (
      <Button xs onClick={() => confirmFoodRecord(id)}>
        <b className="text-success">I ate this</b>
      </Button>
    );

    return (
      <div>
        <FlexWrapper>
          <div>
            <Button xs onClick={() => destroyRecord(id, date)}>
              Remove
            </Button>
            {!confirmed && confirmBtn}
            <span onClick={this.toggleQuants}>
              {` ${Main}, ${Sub} `}
            </span>
          </div>
          <div>
            {this.state.updating
            ? <ModifyRecordForm
              record={food}
              changeUpdating={this.changeUpdating}
            />
            : <Label
              primary
              style={{ cursor: 'pointer' }}
              onClick={this.changeUpdating}
            >
              {Quantity} {Unit}
            </Label>}
          </div>
        </FlexWrapper>
        <CSSTransitionGroup
          transitionName="collapse"
          transitionEnterTimeout={100}
          transitionLeaveTimeout={100}
        >
          {showingQuants
          ? <HeightCollapse
            onClick={this.toggleQuants}
            key={allQuants}
            ms={100}
            height={`${allQuants.length * 1.3}rem`}
          >
            <Column height={`${allQuants.length * 1.3}rem`}>
              {allQuants.map(quantity => (
                <Label info key={quantity}>
                  {quantity}
                </Label>
                )
              )}
            </Column>
          </HeightCollapse>
          : null}
        </CSSTransitionGroup>
        {!allQuants.length
        ? null
        : <div
          className="text-right"
          onClick={this.toggleQuants}
          style={{ color: '#ddd', cursor: 'pointer' }}
        >
          <span className={`glyphicon glyphicon-chevron-${showingQuants ? 'up' : 'down'}`} />
        </div>}
      </div>
    );
  }
}

FoodListItem.propTypes = {
  destroyRecord: PropTypes.func,
  confirmFoodRecord: PropTypes.func,
  food: PropTypes.object,
  date: PropTypes.object
};

const mapStateToProps = ({ root }) => ({
  date: root.date
});


const mapDispatchToProps = dispatch => ({
  destroyRecord: (id, date) => dispatch(destroyFoodRecord(id, date)),
  confirmFoodRecord: id => dispatch(confirmRecord(id)),
});

export default connect(mapStateToProps, mapDispatchToProps)(FoodListItem);

/**
 *
 * @param {{Gr: number: abbrev: {weights: [{Description: string, Unit: string, Description: string, Amount: number, Gr_Wgt: number}]}}} record
 * @return {[string]}
 */
function getQuantities(record) {
  const grams = record.Gr;

  const quants = record.abbrev.weights.filter(weight => weight.Description !== record.Unit && weight.Description !== 'g').map((weight) => {
    const amount = Math.round((grams / (weight.Amount * 1) / (weight.Gr_Wgt * 1)) * 10) / 10;
    return `${amount} ${weight.Description}`;
  });

  if (record.Unit !== 'g') {
    quants.push(`${grams} g`);
  }

  return quants;
}




