import React from 'react';
import axios from 'axios';
import debounce from 'utils/debounce';
import Well from 'components/Well';
import Button from 'components/Button';
import { Select, Label } from 'components/FormComponents';
import ItemDescription from '../components/ItemDescription';
import Serving from '../components/Serving';
import Macronutrients from '../components/Macronutrients';

class AddForm extends React.Component {
  constructor() {
    super();
    this.state = {
      foodName: '',
      servingSize: 1,
      servingWeight: 0,
      calories: 0,
      protein: 0,
      carbs: 0,
      fat: 0,
      suggestedGroup: '',
      nameOK: false,
      nameError: '',
      calError: false
    };

    this.btnDisabled = true;

    this.addNewFood = this.addNewFood.bind(this);
    this.changeName = this.changeName.bind(this);
    this.change = this.change.bind(this);
    this.getOptions = this.getOptions.bind(this);

    this.getFoodGroup = this.getFoodGroup.bind(this);
    this.checkCalPercent = this.checkCalPercent.bind(this);
    this.getFoodGroup = debounce(this.getFoodGroup, 700);
    this.checkCalPercent = debounce(this.checkCalPercent, 700);
  }

  getOptions() {
    console.log(this.state); // eslint-disable-line
  }

  getFoodGroup() {
    if (this.state.foodName.length < 3 || this.state.nameError.length) {
      return;
    }
    axios.get('/api/database/foodgroup', {
      params: { food: this.state.foodName.split(', ') }
    })
    .then(({ data }) => this.setState({
      suggestedGroup: data
    }))
    .catch(console.error); // eslint-disable-line
  }

  changeName(ev) {
    const { value } = ev.target;
    let nameError = false;
    if (value.split(', ').length < 2) {
      nameError = 'There must be at least two descriptive terms separated by a comma (, )';
    } else if (value.split(', ').reduce((memo, word) => memo || word.length === 0, false)) {
      nameError = 'Each descriptive term must have a length greater than zero';
    }
    this.setState({
      foodName: value.replace(/'|"|_/g, '').replace(/[ ][ ]/g, ' '),
      nameError
    }, this.getFoodGroup);
  }

  change(ev, param) {
    const stte = {};
    stte[param] = ev.target.value;

    this.setState(stte, this.checkCalPercent);
  }

  checkCalPercent() {
    this.setState({ calError: Math.abs(((this.state.calories * 1) - ((this.state.protein * 4) + (this.state.carbs * 4) + (this.state.fat * 9))) / (this.state.calories * 1)) > 0.2 });
  }

  addNewFood() {
    console.log(this.state); // eslint-disable-line
  }

  checkName() {
    const separated = this.state.foodName.split(', ');
    this.btnDisabled = separated.length <= 1 || !separated.reduce((tf, word) => tf && word.length > 1, true);
  }

  render() {
    const { servingWeight, servingSize, suggestedGroup } = this.state;
    return (
      <div className="container">
        <h3>Add to the Database</h3>
        <hr />
        <Well>
          <ItemDescription
            foodName={this.state.foodName}
            onchange={this.changeName}
            nameError={this.state.nameError}
          />
        </Well>
        <Well>
          <Label htmlFor="group">Food Group:</Label>
          <Select id="group" onChange={this.getOptions}>
            <option value="">
              Let system decide {suggestedGroup.name ? `(${suggestedGroup.name})` : '(Enter a valid item description)'}
            </option>
          </Select>
        </Well>
        <Well>
          <Serving
            change={this.change}
            servingSize={servingSize}
            servingWeight={servingWeight}
          />
        </Well>
        <Well>
          <Macronutrients
            change={this.change}
            calories={this.state.calories}
            protein={this.state.protein}
            carbs={this.state.carbs}
            fat={this.state.fat}
          />
          <div>
            {this.state.calError &&
              <span className="text-danger">Given calories are not similar to calories calculated from macronutrients</span>
            }
          </div>
        </Well>
        <div className="form-group">
          <Button primary onClick={this.addNewFood} disabled={this.btnDisabled}>
            Submit
          </Button>
        </div>
        <div id="errorsDiv" />
      </div>
    );
  }
}

export default AddForm;



