import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import DayMacroSummary from 'containers/DayMacroSummary';
import ChangeDay from 'containers/ChangeDay';
import FlexWrapper from 'components/FlexWrapper';
import RecordList from './containers/SelectFood';


const HalfWrapper = styled.div`
  width: 45%;
`;

const FoodRecord = ({ date }) => {
  const isToday = date.toDateString() === (new Date()).toDateString();
  return (
    <div className="container">
      <div className="text-center">
        <hr />
        <h3>{isToday
          ? <span className="text-primary">Today</span>
          : <span className="text-warning">{date.toDateString()}</span>
        }</h3>
        <hr />
      </div>
      <FlexWrapper>
        <HalfWrapper>
          <ChangeDay />
        </HalfWrapper>
        <HalfWrapper>
          <DayMacroSummary />
        </HalfWrapper>
      </FlexWrapper>
      <RecordList />
    </div>
  );
};

FoodRecord.propTypes = {
  date: PropTypes.date
};

const mapStateToProps = ({ root }) => ({
  date: root.date
});

export default connect(mapStateToProps)(FoodRecord);



