import styled from 'styled-components';
import color from 'color';
import colorCSS, { active, fixed } from 'colorCSS';

export default styled.div`
  background-color: ${color('#333').lighten(0.99).toString()};
  border-bottom: 3px solid;
  overflow: hidden;
  top: 0px;
  position: fixed;
  width: 100%;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  
  ${fixed}
  
  .container &, .container-fluid & {
    margin-left: -15px;
    margin-right: -15px;
  }

  & a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
    font-size: 15px;

    ${colorCSS}
  }

  & a:not(:first-child):hover {
    ${active}
  }

  & a.active {
    ${active}
  }

  & .icon {
    display: none;
  }

  @media screen and (max-width: 600px) {
    & a:not(.active) {
      display: none;
    }
    & a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 600px) {
    &.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    &.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
`;



