import React from 'react';
import { connect } from 'react-redux';
import Button from 'components/Button';
import PropTypes from 'prop-types';
import FlexWrapper from 'components/FlexWrapper';
import styled from 'styled-components';
import { changeAddMeal } from './dispatch';

const H4 = styled.h4`
  margin: 0px;
`;

/**
 * @param {number} mealIdx
 */
const Header = ({ title, mealIdx, chngMeal }) => (
  <div>
    <FlexWrapper align="center">
      <H4>{title}</H4>
      <Button className="btn-sm" onClick={() => chngMeal(mealIdx)}>
        <span className="glyphicon glyphicon-plus text-success" />
        { ' ' }
        Add Food
      </Button>
    </FlexWrapper>
    {/* <hr style={{ borderColor: '#dedede', margin: '5px 0px' }} /> */}
  </div>
);

Header.propTypes = {
  title: PropTypes.string,
  mealIdx: PropTypes.number,
  chngMeal: PropTypes.func
};

const mapDispatchToProps = dispatch => ({
  chngMeal: idx => dispatch(changeAddMeal(idx))
});

export default connect(null, mapDispatchToProps)(Header);



