import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import ChangeDay from 'containers/ChangeDay';
import Button from 'components/Button';
import Well from 'components/Well';
import Container from 'components/Container';

import { addDay, removeDay, recalculateDay, handleAddDay } from './dispatch';

import Day from './Day';

class ShoppingList extends React.Component {
  constructor() {
    super();

    this.state = { confirmAll: false };
    this.confirmAll = this.confirmAll.bind(this);
  }

  confirmAll() {
    this.props.confirmWeek(this.props.shoppinglist, this.props.date);
    this.setState({ confirmAll: true });
  }

  render() {
    const { addWholeDay, addWholeWeek, date, shoppinglist, fetchingData } = this.props;
    const followingDay = numDays => new Date(date.getTime() + (numDays * 86400000)).toDateString();
    return (
      <Container>
        <h3>Day Meal Planner</h3>
        <hr />
        { fetchingData && <span className="alert alert-warning pull-right"><b>Getting your meal plan</b></span> }
        <ChangeDay />
        <Well>
          <div className="row">
            <div className="col-xs-8">
              <Button primary onClick={() => addWholeDay('rest')}>
                Add Day
              </Button>
              { ' ' }
              <Button primary onClick={() => addWholeWeek('rest')}>
                Add Week
              </Button>
            </div>
            <div className="col-xs-4 text-right">
              <Button success onClick={this.confirmAll}>
                Confirm All
              </Button>
            </div>
          </div>
        </Well>
        <div>
          {
            shoppinglist.map((day, ix) => (day &&
              <Well key={ix}>
                <Day day={day} index={ix} isConfirmed={this.state.confirmAll} date={followingDay(ix)} />
              </Well>
            ))
          }
        </div>
      </Container>
    );
  }
}

ShoppingList.propTypes = {
  shoppinglist: PropTypes.array,
  fetchingData: PropTypes.bool,
  date: PropTypes.object,
  addWholeDay: PropTypes.func,
  addWholeWeek: PropTypes.func,
  confirmWeek: PropTypes.func
};

const mapStateToProps = ({ shoppinglist, root }) => ({
  shoppinglist: shoppinglist.shoppinglist,
  fetchingData: shoppinglist.fetchingData,
  date: root.date
});

const followingDay = (date, numDays) => new Date(date.getTime() + (numDays * 86400000)).toDateString();

const mapDispatchToProps = dispatch => ({
  addWholeDay: type => dispatch(addDay(type)),
  addWholeWeek: (type) => {
    for (let i = 0; i < 7; i++) {
      dispatch(addDay(type));
    }
  },
  confirmWeek: (shoppingList, date) => {
    shoppingList.forEach((meals, ix) => {
      dispatch(handleAddDay(meals, followingDay(date, ix)));
    });
  },
  removeDay: index => dispatch(removeDay(index)),
  recalculateDay: index => dispatch(recalculateDay(index))
});

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingList);



