import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import DayMacroSummary from 'containers/DayMacroSummary';
import Panel from 'components/Panel';
import RecordList from 'containers/FoodRecord/RecordList';
import ChangeDay from 'containers/ChangeDay';
import Container from 'components/Container';
import FlexWrapperBase from 'components/FlexWrapper';
import media from 'theme/media';

import { connect } from 'react-redux';
import { browserHistory } from 'react-router';

import ReactHighcharts from 'react-highcharts';
import { calculateDayMacros } from 'containers/FoodRecord/dispatch';
import { getCalories } from './dispatch';
import bardataFunc from './bardata';
import WeightChart from './WeightChart';

const Column = styled.div`
  padding: 0px 0.5rem;

  &:first-child {
    padding-left: 0px;
  }
  &:last-child {
    padding-right: 0px;
  }
  ${media.lgUp`
    width: ${props => props.width}
  `}
  ${media.lgDown`
    width: 50%;
  `}
  ${media.mdDown`
    padding: 0px;
    width: 100%;
  `}
`;

const FlexWrapper = FlexWrapperBase.extend`
  align-items: stretch;
  height: 100vh;
`;

const MainDash = ({ date, foodrecord, user, calories }) => {
  if (!user.mealGoals.length) {
    browserHistory.push('/modify-goals');
    return null;
  }

  const isToday = date.toDateString() === new Date().toDateString();
  const bardata = bardataFunc(foodrecord);
  const titleStyle = isToday ? {} : { color: '#f8d104' };
  const dateStr = isToday ? 'Today' : date.toDateString();
  const consumed = calculateDayMacros(foodrecord).raw.calories;

  return (
    <Container fluid>
      Calories burned: {calories}
      {' | '}
      Calories consumed: {consumed}
      {' | '}
      Balance: {calories - consumed}
      <FlexWrapper align="stretch">
        <Column width="25%">
          <Panel title={dateStr} primary titleStyle={titleStyle}>
            <DayMacroSummary />
          </Panel>
          <Panel title="Macronutrients By Meal" primary>
            <ReactHighcharts config={bardata} />
          </Panel>
        </Column>
        <Column width="45%">
          <RecordList />
        </Column>
        <Column width="30%">
          <Panel title="Change Day" primary>
            <ChangeDay />
          </Panel>
          <Panel title="Weight Progress" primary>
            <WeightChart />
          </Panel>
        </Column>
      </FlexWrapper>
    </Container>
  );
};

MainDash.defaultProps = {
  date: new Date()
};

MainDash.propTypes = {
  date: PropTypes.object,
  user: PropTypes.object,
  foodrecord: PropTypes.array,
  calories: PropTypes.number
};

const mapStateToProps = ({ root, foodrecord }) => ({
  date: root.date,
  foodrecord: foodrecord.record,
  user: root.user,
  calories: foodrecord.calories
});

const mapDispatchToProps = dispatch => ({
  getCalories: startDate => dispatch(getCalories(startDate))
});

export default connect(mapStateToProps, mapDispatchToProps)(MainDash);



