import styled from 'styled-components';
import colorCSS from 'colorCSS';

export default styled.button`
  font-size: 16px;
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background: #fff;
  border: 1px solid #ccc;
  white-space: nowrap;
  border-radius: 3px;
  padding: 6px 10px;

  ${props => (props.sm
    ? `
    padding: 3px 7px;
    font-size: 14px;
    line-height: 1.5;
    `
    : null)
  };

  ${props => (props.xs
    ? `
    padding: 1px 5px;
    font-size: 12px;
    line-height: 1.5;
    `
    : null)
  };

  ${colorCSS}
`;



