import { CALCULATE_MEAL_ERROR, CALCULATE_MEAL_SUCCESS, LOAD_SEARCHED_FOODS, REMOVE_FOOD, RETAIN_FOOD, ADD_TO_SEARCH_LIST } from '../constants';

const initialState = {
  searchedFoods: [],
  retainedFoods: [],
  meal: [],
  lastSearch: '',
  error: null
};

export default (state = initialState, action) => {
  let newState;
  switch (action.type) {

    case LOAD_SEARCHED_FOODS:
      return { ...state, searchedFoods: action.payload, lastSearch: action.lastSearch };

    case RETAIN_FOOD:
      newState = { ...state, retainedFoods: state.retainedFoods.slice() };
      newState.searchedFoods = state.searchedFoods.filter(fd => fd.id !== action.payload.id);
      newState.retainedFoods.push(action.payload);
      return newState;

    case REMOVE_FOOD:
      newState = { ...state, retainedFoods: state.retainedFoods.slice() };
      newState.retainedFoods = state.retainedFoods.filter(fd => fd.id !== action.payload);
      return newState;

    case ADD_TO_SEARCH_LIST:
      newState = { ...state, searchedFoods: state.searchedFoods.slice() };
      newState.searchedFoods.splice(0, 0, action.payload);
      return newState;

    case CALCULATE_MEAL_SUCCESS:
      newState = Object.assign({}, state, action.payload);
      if (newState.error) delete newState.error;
      return newState;

    case CALCULATE_MEAL_ERROR:
      return { ...state, error: action.payload, meal: [] };

    default:
      return state;
  }
};




