import api from 'utils/api';
import caloriesCache from 'utils/caloriesCache';
import debounce from 'utils/debounce';

import { RECEIVE_CALORIES, DESTROY_MEASUREMENT } from 'app/redux/constants';

/**
 * Get calories from the cache
 * @param {Date} startDate
 */
const receiveCalories = (startDate = new Date()) => (dispatch) => {
  dispatch({ type: RECEIVE_CALORIES, payload: caloriesCache[startDate] });
};

const getCalsDb = debounce((startDate, endDate, dispatch) => api
  .get('/api/fitbit/calories', {
    startDate: caloriesCache.convertDate(startDate),
    endDate: caloriesCache.convertDate(endDate || startDate)
  })
  .then(({ data }) => {
    caloriesCache.bulkAddRecord(data);
    dispatch({ type: RECEIVE_CALORIES, payload: data[0].value * 1 });
  })
, 300);

/**
 * Retrieve calories
 * @param {Date} startDate
 * @param {Date} endDate
 * @return {AxiosPromise}
 */
export const getCalories = (startDate, endDate) => (dispatch) => {
  if (!isNaN(caloriesCache[caloriesCache.convertDate(startDate)] + 1)) {
    const strtDate = caloriesCache.convertDate(startDate);
    dispatch(receiveCalories(strtDate));
  }
  if (startDate && caloriesCache.hasRecord(caloriesCache.convertDate(startDate))) {
    dispatch(receiveCalories(startDate));
    return Promise.resolve();
  }

  return getCalsDb(startDate, endDate, dispatch);

  // return api.get('/api/fitbit/calories', { params: {
  //   token: getToken(),
  //   startDate: caloriesCache.convertDate(startDate),
  //   endDate: caloriesCache.convertDate(endDate || startDate)
  // } })
  // .then(({ data }) => {
  //   caloriesCache.bulkAddRecord(data);
  //   dispatch({ type: RECEIVE_CALORIES, payload: data[0].value * 1 });
  // });
};

/**
 * Remove a measurement from the database
 * @param {number} measId - Measurement id (integer)
 */
export const destroyMeasurement = measId => dispatch => api
.delete('/api/user/measurements', {
  data: {
    id: measId
  }
})
.then(() => dispatch({ type: DESTROY_MEASUREMENT, payload: measId }));

export const hideFoodModal = () => dispatch => dispatch({ type: 'HIDE_MODAL' });



