import { RECEIVE_MEALS } from '../constants';

const initialState = {
  meals: []
};

export default (state = initialState, action) => {
  switch (action.type) {

    case RECEIVE_MEALS:
      return { ...state, meals: action.payload };

    default:
      break;
  }
  return state;
};




