import api from 'utils/api';

import { INVALID_LOGIN, LOGIN_SUCCESS } from 'app/redux/constants';

const loginFail = () => ({ type: INVALID_LOGIN });
const invalidLogin = () => dispatch => dispatch(loginFail());
const loginSuccess = user => ({ type: LOGIN_SUCCESS, user });


/** Get the user from the database */
export const exchangeTokenForUser = () => (dispatch) => {
  if (!localStorage.getItem('token')) {
    return console.warn('No local storage token'); // eslint-disable-line
  }
  return api.get(`/api/session/${localStorage.getItem('token')}`)
    .then(({ data }) => {
      api.defaults.headers.token = localStorage.getItem('token');
      dispatch(loginSuccess(data));
      return data;
    });
};

/**
 * Log in
 * @param {object} credentials - Login credentials
 */
export const login = credentials => dispatch => api
.post('api/session', credentials)
.then(response => response.data)
.then(({ token }) => localStorage.setItem('token', token))
.then(() => dispatch(exchangeTokenForUser()))
.catch(() => {
  localStorage.removeItem('token');
  dispatch(invalidLogin(loginFail()));
});



