import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { login } from 'containers/Login/dispatch';
import { connect } from 'react-redux';
import axios from 'axios';
import FlexWrapper from 'components/FlexWrapper';
import OptionGroup from 'components/OptionGroup';
import Button from 'components/Button';
import { Input } from 'components/FormComponents';
import { MarginWrapper, FlexInput, FlexOptGrp } from './styled';

const initialState = {
  username: '',
  password: '',
  firstname: '',
  lastname: '',
  email: '',
  birthdate: '',
  gender: 'male',
  height: '',
  hunit: 'inch',
  weight: '',
  wunit: 'lbs',
  bodyfat: '30',
  lifestyle: 'Normal',
  goal: 'Lose Fat'
};

class SignupForm extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;

    this.change = this.change.bind(this);
    this.newAccount = this.newAccount.bind(this);
  }

  change(param) {
    return (ev) => {
      const mm = {};
      mm[param] = ev.target.value;
      this.setState(mm);
    };
  }

  newAccount(ev) {
    ev.preventDefault();
    axios.post('api/user/signup', this.state)
    .then((user) => {
      if (user) {
        return this.props.login({ username: this.state.username, password: this.state.password });
      }
      return null;
    })
    .then(() => {
      if (!this.props.invalidLogin) {
        const newState = initialState;
        this.setState(newState);
        this.props.hide();
        this.props.hideLogin();
      }
    });
  }

  render() {
    return (
      <div>
        <div className="buffer">
          <div>
            <h3><i>Account Info</i></h3>
            <div>
              <MarginWrapper>
                <Input
                  placeholder="user name"
                  value={this.state.username}
                  onChange={this.change('username')}
                />
              </MarginWrapper>
              <MarginWrapper>
                <Input
                  type="password"
                  placeholder="password"
                  value={this.state.password}
                  onChange={this.change('password')}
                />
              </MarginWrapper>
              <MarginWrapper>
                <Input
                  placeholder="first name"
                  value={this.state.firstname}
                  onChange={this.change('firstname')}
                />
              </MarginWrapper>
              <MarginWrapper>
                <Input
                  placeholder="last name"
                  value={this.state.lastname}
                  onChange={this.change('lastname')}
                />
              </MarginWrapper>
              <MarginWrapper>
                <Input
                  type="email"
                  placeholder="email"
                  value={this.state.email}
                  onChange={this.change('email')}
                />
              </MarginWrapper>

              <MarginWrapper />
              <h3><i>Tell Us About Yourself</i></h3>
              <i>Gender</i>
              <MarginWrapper>
                <OptionGroup
                  placeholder="gender"
                  onChange={this.change('gender')}
                  options={['male', 'female']}
                />
              </MarginWrapper>

              <i>Birthdate</i>
              <MarginWrapper>
                <Input
                  id="date"
                  type="date"
                  placeholder="birth date"
                  value={this.state.birthdate}
                  onChange={this.change('birthdate')}
                />
              </MarginWrapper>

              <MarginWrapper>
                <FlexWrapper>
                  <FlexInput
                    type="number"
                    placeholder="height"
                    value={this.state.height}
                    onChange={this.change('height')}
                  />
                  <FlexOptGrp
                    placeholder="gender"
                    onChange={this.change('hunit')}
                    options={['inch', 'cm']}
                  />
                </FlexWrapper>

                <FlexWrapper>
                  <FlexInput
                    type="number"
                    placeholder="weight"
                    value={this.state.weight}
                    onChange={this.change('weight')}
                  />
                  <FlexOptGrp
                    placeholder="gender"
                    onChange={this.change('wunit')}
                    options={['lbs', 'kg']}
                  />
                </FlexWrapper>
              </MarginWrapper>

              <MarginWrapper>
                <Input
                  placeholder="body fat"
                  value={this.state.bodyFat}
                  onChange={this.change('bodyfat')}
                />
              </MarginWrapper>

              <MarginWrapper>
                <i>Your Lifestyle</i>
                <OptionGroup
                  onChange={this.change('lifestyle')}
                  options={['Normal', 'Active', 'Sedentary']}
                />
              </MarginWrapper>

              <MarginWrapper>
                <i>Your Goal</i>
                <OptionGroup
                  placeholder="goal"
                  onChange={this.change('goal')}
                  options={['Lose Fat', 'Gain Muscle', 'Maintain']}
                />
              </MarginWrapper>
            </div>
            <div className="buffer forms"></div>
            <div className="buffer">
              <Button onClick={this.newAccount} primary className="btn-social">
                <i className="fa fa-caret-square-o-up" />
                <span>Sign Up</span>
              </Button>
            </div>

          </div>
        </div>
      </div>

    );
  }
}

SignupForm.propTypes = {
  hide: PropTypes.func,
  login: PropTypes.func,
  hideLogin: PropTypes.func,
  invalidLogin: PropTypes.bool
};

const mapStateToProps = ({ root }) => ({
  user: root.user,
  invalidLogin: root.invalidLogin
});

const mapDispatchToProps = dispatch => ({
  login: credentials => dispatch(login(credentials)),
});

export default connect(mapStateToProps, mapDispatchToProps)(SignupForm);



