import api from 'utils/api';
import { ADD_DAY, FETCHING_DAY_MEAL_PLAN, RECALCULATE_DAY, REMOVE_DAY, RECEIVE_FOOD_RECORD } from 'app/redux/constants';

/**
 * Get new foods from the database and calculate based on goals
 * @param {number} index - (integer)
 * @param {string} type - Should be 'train' or 'rest'
 */
export const recalculateDay = (index, type) => (dispatch) => {
  dispatch({ type: FETCHING_DAY_MEAL_PLAN, payload: true });
  return api.get('/api/calculate/day', {
    params: {
      type
    }
  })
  .then(({ data }) => dispatch({
    type: RECALCULATE_DAY,
    payload:
    { index, day: data }
  }));
};

/**
 * Remove a day
 * @param {number} index - (integer)
 */
export const removeDay = index => dispatch => dispatch({ type: REMOVE_DAY, payload: index });

/** Take the weight from food records and round them to 1/10
 * @param {{ weights: {gr: number, Gr_Wgt: number, Amount: number}}} food
 * @param {{gr: number}} weight
 */
export const modWeight = (food, weight) => food
  .weights
  .map(wt => Math.round((weight.gr / (wt.Gr_Wgt * 1)) * (wt.Amount * 1) * 10) / 10);

/** Add all food records for the day to the database
 * @param {[[{foods: [{weight: number, weights:[{Seq: number}], id: number}]}]]} dayMeals
 * @param {Date|string} date - Instance of Date, or a string
 */
export const handleAddDay = (dayMeals, date) => (dispatch) => {
  dayMeals.forEach((meal, ix) => {
    if (meal) {
      meal.forEach((factor) => {
        factor.foods.forEach((food) => {
          const quantities = modWeight(food, factor.weight);
          api.post('/api/food-record', {
            abbrev_id: food.id,
            date: typeof date === 'string' ? date : date.toDateString(),
            meal: ix + 1,
            quantity: quantities[0],
            unit: food.weights[0].Seq,
            confirmed: false
          })
          .then(({ data }) => {
            dispatch({
              type: RECEIVE_FOOD_RECORD,
              payload: data,
              date
            });
          });
        });
      });
    }
  });
};

/**
 * Add a day to the shopping list, with calculations complete
 * @param {string} type - Should be 'train' or 'rest'
 */
export const addDay = type => (dispatch) => {
  dispatch({ type: FETCHING_DAY_MEAL_PLAN, payload: true });
  return api.get('/api/calculate/day', { params: { type } })
    .then(({ data }) => dispatch({ type: ADD_DAY, payload: data }));
};



