const getWeight = (record) => {
  const unit = record
    .abbrev
    .weights
    .filter(weight => weight.Seq === record.Unit)[0];

  return ((unit.Gr_Wgt * 1) * (record.Quantity * 1)) / (unit.Amount * 1);
};

const combineRecords = list => Object.values(list.reduce((memo, record) => {
  const mm = { ...memo };
  const { abbrev } = record;
  const grWht = getWeight(record);
  if (memo[abbrev.id]) {
    mm[abbrev.id] = memo[abbrev.id].gr + grWht;
  } else {
    mm[abbrev.id] = Object.assign(record, { gr: grWht });
  }
  return mm;
}, {}));


export const makeList = list => combineRecords(list).reduce((memo, record) => {
  const mm = { ...memo };
  const foodGroup = record.abbrev.foodDesc.foodGroup.Description;
  if (memo[foodGroup]) {
    mm[foodGroup].push(record);
  } else {
    mm[foodGroup] = [record];
  }
  return mm;
}, {});

