import React from 'react';
import PropTypes from 'prop-types';
import FlexWrapper from 'components/FlexWrapper';
import Macronutrient from './Macronutrient';

const Macronutrients = ({ calories, protein, carbs, fat, change }) => (
  <FlexWrapper>
    <Macronutrient
      label="Calories"
      change={change}
      macro={calories}
      param="calories"
    />
    <Macronutrient
      label="Protein"
      change={change}
      macro={protein}
      param="protein"
    />
    <Macronutrient
      label="Carbs"
      change={change}
      macro={carbs}
      param="carbs"
    />
    <Macronutrient
      label="Fat"
      change={change}
      macro={fat}
      param="fat"
    />
  </FlexWrapper>
);

Macronutrients.propTypes = {
  calories: PropTypes.string,
  carbs: PropTypes.string,
  protein: PropTypes.string,
  fat: PropTypes.string,
  change: PropTypes.func
};

export default Macronutrients;



