import React from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider } from 'styled-components';

import {
  browserHistory,
  Route,
  Router
} from 'react-router';

import Home from 'containers/Home';
import FoodRecord from 'containers/FoodRecord';
import MealPlanner from 'containers/MealPlanner';
import ShoppingList from 'containers/ShoppingList';
import PublicMeals from 'containers/PublicMeals';
import Database from 'containers/Database';
import List from 'containers/List';
import theme from 'theme/index';
import SetGoals from 'containers/SetGoals';
import { Provider } from 'react-redux';
import { exchangeTokenForUser } from 'containers/Login/dispatch';

import store from '../redux/store';
import Main from './Main';

const init = () => {
  store.dispatch(exchangeTokenForUser());
};

// for oauth
if (document.location.search.indexOf('token=') === 1) {
  const token = document.location.search.slice(1).split('&')[0].split('token=')[1];
  window.localStorage.setItem('token', token);
  browserHistory.push('/');
}

// const App = () => (

// );
const render = () => {
  ReactDOM.render(
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <Router history={browserHistory}>
          <Route component={Main} onEnter={init}>
            <Route path="/" component={Home} />
            <Route path="/food-record" component={FoodRecord} />
            <Route path="/meal-planner" component={MealPlanner} />
            <Route path="/shopping-list" component={ShoppingList} />
            <Route path="/public-meals" component={PublicMeals} />
            <Route path="/modify-goals" component={SetGoals} />
            <Route path="/list" component={List} />
            <Route path="/database" component={Database} />
          </Route>
        </Router>
      </ThemeProvider>
    </Provider>, document.getElementById('app'));
};

render();



