import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Button from 'components/Button';
import { Select } from 'components/FormComponents';
import { MEALS } from 'app/redux/constants';
import { removeDay, recalculateDay } from './dispatch';
import AddDay from './AddDay';

class Day extends React.Component {
  constructor() {
    super();
    this.state = { type: 'rest', added: false };
    this.onChangeType = this.onChangeType.bind(this);
    this.confirmAddDay = this.confirmAddDay.bind(this);
  }

  componentWillReceiveProps(newProps) {
    if (newProps.isConfirmed) {
      this.setState({ added: true });
    }
  }

  onChangeType(ev) {
    this.setState({ type: ev.target.value }, () => {
      this.props.recalculateDay(this.props.index, this.state.type);
    });
  }

  confirmAddDay() {
    this.setState({ added: true });
  }

  render() {
    const meals = MEALS.slice();
    function modWeight(food, weight) {
      return food.weights.map(wt => `${Math.round((weight / (wt.Gr_Wgt * 1)) * (wt.Amount * 1) * 10) / 10} ${wt.Description}`);
    }
    return (
      <div>
        {(this.state.added || this.props.isConfirmed) && <div className="alert alert-success"><b>Meals Added</b></div>}
        <div className="row">
          <div className="col-xs-4">
            <b>Day { this.props.index + 1 }</b>
          </div>
          <div className="col-xs-4 text-center">
            {this.props.date}
          </div>
          <div className="col-xs-4 text-right">
            <AddDay meals={this.props.day} day={this.props.date} clickAddDay={this.confirmAddDay} />
            <Button default xs onClick={() => this.props.recalculateDay(this.props.index, this.state.type)}>
              <span className="glyphicon glyphicon-refresh text-primary" />
            </Button>
            <Button default xs onClick={() => this.props.removeDay(this.props.index)}>
              <span className="glyphicon glyphicon-remove text-danger" />
            </Button>
          </div>
        </div>
        <Select value={this.state.type} onChange={this.onChangeType}>
          <option value="rest">Rest</option>
          <option value="train">Train</option>
        </Select>
        <ul className="list-group">
          { this.props.day.map((meal, ix) => (
            meal &&
            (<li key={ix} className="list-group-item">
              <b>{meals[ix]}</b>
              <br />
                {
                  meal.map(factor => (
                    factor.foods.map((food, idx) => (
                      <div key={idx}>
                        {food.longname}
                        <span className="badge badge-default pull-right">
                          {modWeight(food, (factor.weight.gr / factor.foods.length))[0]}
                        </span>
                      </div>
                    ))
                  ))
                }
            </li>)
            )
          ) }
        </ul>
      </div>
    );
  }
}

Day.propTypes = {
  recalculateDay: PropTypes.func,
  index: PropTypes.number,
  isConfirmed: PropTypes.bool,
  date: PropTypes.object,
  day: PropTypes.array,
  removeDay: PropTypes.func
};

const mapDispatchToProps = dispatch => ({
  removeDay: index => dispatch(removeDay(index)),
  recalculateDay: (index, type) => dispatch(recalculateDay(index, type))
});


export default connect(null, mapDispatchToProps)(Day);



