import React from 'react';
import PropTypes from 'prop-types';
import Button from 'components/Button';
import { Overlay, ModalContent } from './styled';

class ModalCoverage extends React.Component {
  constructor() {
    super();
    this.state = {
      display: false
    };
    this.hideModal = this.hideModal.bind(this);
  }

  componentWillReceiveProps(newProps) {
    if (newProps.display !== this.state.display) {
      this.setState({ display: newProps.display });
    }
  }

  hideModal() {
    this.setState({ display: false }, () => {
      if (this.props.overlayClick) {
        this.props.overlayClick();
      }
    });
  }

  displayModal() {
    this.setState({ display: true }, () => {
      if (this.props.onDisplayModal) {
        this.props.onDisplayModal();
      }
    });
  }

  render() {
    if (!this.state.display) return null;

    return (
      <Overlay onClick={this.hideModal}>
        <ModalContent onClick={ev => ev.stopPropagation()}>
          <Button onClick={this.hideModal}>Close</Button>
          { this.props.children }
        </ModalContent>
      </Overlay>
    );
  }
}

ModalCoverage.propTypes = {
  children: PropTypes.node,
  overlayClick: PropTypes.func,
  onDisplayModal: PropTypes.func
};

export default ModalCoverage;



