import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { generateFood, calculateFood } from 'reducers/fpdietgenerator';
import Well from 'components/Well';
import { Input } from 'components/FormComponents';
import OptionGroup from 'components/OptionGroup';
import NutrientsSection from './NutrientsSection';
import MealSection from './MealSection';
import filteredMeals from '../../../../filteredmeals.json';

class InputsSection extends Component {
  constructor(props) {
    super(props);

    this.state = {
      age: '',
      gender: 'Male',
      height: '',
      cmheight: '',
      hunit: 'feet\' inches',
      weight: '',
      wunit: 'lbs',
      lifestyle: 'Normal',
      goal: 'lose 4 pounds in 1 month',
      BMR: '',
      recommendation: {
        calories: null,
        protein: null,
        carbs: null,
        fat: null,
        foodChoice: null
      },
      generatedFood: filteredMeals
    };
    this.onHeightChange = this.onHeightChange.bind(this);
    this.onCreateNutrients = this.onCreateNutrients.bind(this);
    this.calculateRecommendation = this.calculateRecommendation.bind(this);
    this.onCreateMeals = this.onCreateMeals.bind(this);
    this.change = this.change.bind(this);
  }

  onCreateMeals(ev) {
    if (ev) {
      ev.preventDefault();
    }

    this.props.calculateFood({
      proteinGoal: this.state.recommendation.protein,
      carbGoal: this.state.recommendation.carbs,
      fatGoal: this.state.recommendation.fat
    });
  }

  onCreateNutrients(ev) {
    if (ev) {
      ev.preventDefault();
    }
    let weight;
    let gender;
    if (this.state.gender === 'Male') {
      gender = 5;
    } else {
      gender = -161;
    }
    const height = this.state.cmheight / 100;

    if (this.state.wunit === 'lbs') {
      weight = this.state.weight * 0.45359237;
    } else {
      weight = this.state.weight;
    }

    let lifestyle;
    switch (this.state.lifestyle) {
      case 'Normal':
        lifestyle = 1.375;
        break;
      case 1.375:
        lifestyle = 1.375;
        break;
      case 'Active':
        lifestyle = 1.55;
        break;
      case 1.55:
        lifestyle = 1.55;
        break;
      default:
        lifestyle = 1.2;
    }

    let goal;
    switch (this.state.goal) {
      case 'lose 4 pounds in 1 month':
        goal = -500;
        break;
      case -500:
        goal = -500;
        break;
      case 'lose 8 pounds in 1 month':
        goal = -1000;
        break;
      case -1000:
        goal = -1000;
        break;
      default:
        goal = 0;
    }

    this.setState({
      lifestyle,
      goal,
      BMR: Math.round(((10 * weight) + (625 * height) + gender) - (5 * this.state.age))
    }, this.calculateRecommendation);
  }

  onHeightChange(ev) {
    let { value } = ev.target;
    value = value.replace(/ /g, '');
    if (value.indexOf('\'') > -1) {
      const [ft, inc] = value.split('\'');
      const totalIn = (ft * 12) + (inc ? inc.replace(/"/g, '') * 1 : 0);
      this.setState({ cmheight: 2.54 * totalIn, height: value, hunit: 'feet\' inches' });
    } else if (value * 1 < 12) {
      this.setState({ cmheight: value * 12 * 2.54, height: value, hunit: 'feet\' inches' });
    } else {
      this.setState({ cmheight: value * 1, height: value, hunit: 'centimeters' });
    }
  }

  change(param) {
    return (ev) => {
      const obj = {};
      obj[param] = ev.target.value;
      this.setState(obj);
    };
  }
  calculateRecommendation() {
    const calories = Math.round((this.state.BMR * this.state.lifestyle) + this.state.goal);
    const protein = Math.round((calories * 0.3) / 4);
    const carbs = Math.round((calories * 0.4) / 4);
    const fat = Math.round((calories * 0.3) / 9);
    this.setState({
      recommendation: {
        calories,
        protein,
        carbs,
        fat
      }
    });
  }

  render() {
    return (
      <div className="container">
        <Well>
          <div className="row">
            <div className="col-md-6">

              <div style={{ display: 'inline' }}>
                <h3 style={{ display: 'inline' }}>
                  <i>I want to</i>
                </h3>
                {' '}
                <OptionGroup
                  style={{ width: '215px', display: 'inline' }}
                  value={this.state.goal}
                  onChange={this.change('goal')}
                  options={['lose 4 pounds in 1 month', 'lose 8 pounds in 1 month']}
                />
              </div>

              <div><br /></div>

              <div className="form-inline">
                <h3 style={{ display: 'inline' }}>
                  <i>I am</i>
                </h3>
                {' '}
                <OptionGroup
                  inline
                  value={this.state.gender}
                  onChange={this.change('gender')}
                  options={['Male', 'Female']}
                />
                {' '}
                <h3 style={{ display: 'inline' }}>
                  <i>age</i>
                </h3>
                {' '}
                <Input
                  type="number"
                  min="0"
                  inline
                  placeholder="age"
                  value={this.state.age}
                  onChange={this.change('age')}
                />
              </div>

              <div><br /></div>

              <div className="form-inline">
                <h3 style={{ display: 'inline' }}>
                  <i>My height is</i>
                </h3>
                {' '}
                <Input
                  type="text"
                  min="0"
                  inline
                  placeholder="#'# or #"
                  value={this.state.height}
                  onChange={this.onHeightChange}
                />
                {' '}
                <h3 style={{ display: 'inline' }}>
                  <i>{this.state.hunit}</i>
                </h3>
              </div>

              <div><br /></div>

              <div className="form-inline">
                <h3 style={{ display: 'inline' }}>
                  <i>My weight is</i>
                </h3>
                {' '}
                <Input
                  type="number"
                  min="0"
                  max="500"
                  inline
                  value={this.state.weight}
                  onChange={this.change('weight')}
                />
                <OptionGroup
                  inline
                  value={this.state.wunit}
                  onChange={this.change('wunit')}
                  options={['lbs', 'kg']}
                />
              </div>
              <br />

              <div className="form-inline">
                <h3 style={{ display: 'inline' }}>
                  <i>My lifestyle is</i>
                </h3>
                {' '}
                <OptionGroup
                  inline
                  value={this.state.lifestyle}
                  onChange={this.change('lifestyle')}
                  options={['Normal', 'Active', 'Sedentary']}
                />
              </div>
            </div>

            <div className="col-md-6">
              <button
                style={{ marginTop: '10px' }}
                className="btn btn-info btn-block"
                onClick={this.onCreateNutrients}
              >
                <h3>
                  Calculate My Nutrients
                </h3>
              </button>

              {this.state.BMR
                ? <div>
                  <NutrientsSection diet={this.state.recommendation} />
                </div>
                : null}
            </div>
          </div>
          <div className="row">
            {this.state.BMR
              ? <div className="col-xs-12 form-group">
                <button
                  style={{ marginTop: '20px' }}
                  className="btn btn-info btn-block"
                  onClick={this.onCreateMeals}
                >
                  <h3>
                    Create My Diet
                  </h3>
                </button>
              </div>
              : null}

            <div className="buffer forms">
              {this.state.BMR && this.props.meal.length
                ? <div>
                  <MealSection
                    foods={this.props.generatedFoods}
                    meal={this.props.meal}
                    error={this.props.error}
                  />
                </div>
                : null}
            </div>

          </div>
        </Well>
      </div>
    );
  }
}

InputsSection.propTypes = {
  calculateFood: PropTypes.func,
  meal: PropTypes.array,
  generatedFoods: PropTypes.array,
  error: PropTypes.func
};

const mapStateToProps = ({ fpdietgenerator }) => ({
  generatedFoods: fpdietgenerator.generatedFoods,
  meal: fpdietgenerator.meal,
  error: fpdietgenerator.error
});

const mapDispatchToProps = dispatch => ({
  generateFood: foodChoice => dispatch(generateFood(foodChoice)),
  calculateFood: params => dispatch(calculateFood(params))
});

export default connect(mapStateToProps, mapDispatchToProps)(InputsSection);



