import {
  ADD_DAY,
  FETCHING_DAY_MEAL_PLAN,
  REMOVE_DAY,
  RECALCULATE_DAY,
  RECEIVE_LIST_ITEMS
} from '../constants';


const initialState = {
  shoppinglist: [],
  fetchingData: false,
  list: []
};

export default (state = initialState, action) => {
  const nstate = Object.assign({}, state);

  switch (action.type) {

    case ADD_DAY:
      nstate.shoppinglist = state.shoppinglist.slice();
      nstate.shoppinglist.push(action.payload);
      return nstate;

    case FETCHING_DAY_MEAL_PLAN:
      return state;

    case REMOVE_DAY:
      nstate.shoppinglist = state.shoppinglist.slice();
      nstate.shoppinglist.splice(action.payload, 1);
      return nstate;

    case RECALCULATE_DAY:
      nstate.shoppinglist = state.shoppinglist.slice();
      nstate.shoppinglist[action.payload.index] = action.payload.day;
      return nstate;

    case RECEIVE_LIST_ITEMS:
      nstate.list = action.payload;
      return nstate;

    default:
      return state;
  }
};



