import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import StyledNav from 'components/Nav';
import FlexWrapperBase from 'components/FlexWrapper';
import media from 'theme/media';
import { logout } from './dispatch';
import List from '../../../assets/svg/list.svg';
import Scale from '../../../assets/svg/scale.svg';
import Home from '../../../assets/svg/home.svg';

const FlexWrapper = FlexWrapperBase.extend`
  min-width: 60%;

  ${media.smDown`
    flex-direction: column;
    align-items: flex-start;
    width: 100%;

    & a {
      width: 100%;
    }
    
    & a:not(:first-child) {
      border-top: 1px dashed white;
    }
  `}
`;

class Nav extends React.Component {
  constructor() {
    super();

    this.state = {
      extended: false
    };

    this.updateExtended = this.updateExtended.bind(this);
  }

  updateExtended() {
    this.setState({ extended: !this.state.extended });
  }

  render() {
    return (
      <StyledNav className={this.state.extended ? 'responsive' : null} primary>
        <FlexWrapper>
          <Link to="/" activeClassName="active">
            <Home fill="#fff" stroke="#fff" height="1em" /> Home
          </Link>
          <Link to="/modify-goals" icon="wrench" activeClassName="active">
            Goals
          </Link>
          <Link to="/meal-planner" activeClassName="active">
            <Scale fill="#fff" stroke="#fff" height="1em" /> Single Meal Planner
          </Link>
          <Link to="/shopping-list" icon="screenshot" activeClassName="active">
            Day Meal Planner
          </Link>
          <Link to="/public-meals" icon="globe" activeClassName="active">
            Public Meals
          </Link>
          <Link to="/list" activeClassName="active">
            <List fill="#fff" stroke="#fff" height="1em" /> Shopping List
          </Link>
          <Link to="/database" icon="list" activeClassName="active">
            Database
          </Link>
          <a id="logInClick" onClick={this.props.logout}>
            <span className="glyphicon glyphicon-log-in" />
            {' '}
            Log Out
          </a>
        </FlexWrapper>
        <div>
          <a className="icon" onClick={this.updateExtended}>
            &#9776;
          </a>
        </div>
      </StyledNav>
    );
  }
}

Nav.propTypes = {
  logout: PropTypes.func
};

const mapStateToProps = ({ root }) => ({ user: root.user });

const mapDispatchToProps = dispatch => ({
  logout: () => dispatch(logout())
});

export default connect(mapStateToProps, mapDispatchToProps)(Nav);



