import { fetchFoodRecord } from 'containers/FoodRecord/dispatch';
import { getCalories } from 'containers/Home/dispatch';
import { CHANGE_DAY } from 'app/redux/constants';

/**
 * Update the date for the whole application
 * @param {Date} dateObj
 */
export const changeDay = dateObj => (dispatch) => {
  dispatch({ type: CHANGE_DAY, payload: dateObj });
  dispatch(fetchFoodRecord(dateObj.toDateString()));
  dispatch(getCalories(dateObj));
};

const oneDay = 60 * 60 * 24 * 1000;

/** Forward increment one day for the whole application */
export const nextDayClick = currentDay => (dispatch) => {
  const nextDay = new Date(currentDay.getTime() + oneDay);
  dispatch(changeDay(nextDay));
};

/** Backward increment one day for the whole application */
export const previousDayClick = currentDay => (dispatch) => {
  const previousDay = new Date(currentDay.getTime() - oneDay);
  dispatch(changeDay(previousDay));
};



