import React from 'react';
import PropTypes from 'prop-types';
import Th from 'components/Th';
import { MEALS } from 'app/redux/constants';

const GoalsReport = ({ goals, title }) => {
  if (!goals) return null;

  const mealNames = MEALS.slice();

  const gls = goals.map((meal, ix) => ({ ...meal, id: ix })).filter(meal => (meal.pGoal > 0 || meal.cGoal > 0 || meal.fGoal > 0));
  const calories = ({ pGoal, cGoal, fGoal }) => Math.round((pGoal * 4) + (cGoal * 4) + (fGoal * 9));

  return (
    <div>
      <h4>{ title }</h4>
      <table className="table table-condensed">
        <thead>
          <tr>
            <Th primary>Meal</Th>
            <Th primary>Protein</Th>
            <Th primary>Carbs</Th>
            <Th primary>Fat</Th>
            <Th primary>Calories</Th>
          </tr>
        </thead>
        <tbody>
          {gls.map(meal => (
            <tr key={meal.id}>
              <td>{ mealNames[meal.id] }</td>
              <td>{ meal.pGoal }</td>
              <td>{ meal.cGoal }</td>
              <td>{ meal.fGoal }</td>
              <td>{ calories(meal) }</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

GoalsReport.propTypes = {
  goals: PropTypes.array,
  title: PropTypes.string
};

export default GoalsReport;



