import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Select, Input } from 'components/FormComponents';
import { saveMeasurements, bmrCalories } from './dispatch';
import MeasurementsForm from './components/MeasurementsForm';
import { lifeStyle, invertLifeStyle } from './utils';


class BMRCalculator extends React.Component {
  constructor() {
    super();
    this.state = {
      units: 'imperial',
      weight: '',
      height: '',
      gender: 'Male',
      age: '',
      lifestyle: 1.375,
      BMR: '',
      recommendation: {
        calories: null,
        protein: null,
        carbs: null,
        fat: null
      }
    };

    ['change', 'calculateBMR', 'makeUserState', 'onSaveMeasurements', 'updateLifestyle'].forEach((method) => {
      this[method] = this[method].bind(this);
    });
  }

  componentDidMount() {
    if (this.props.user && this.props.user.userMeasurements && this.props.user.userMeasurements[0]) {
      this.makeUserState();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.user && nextProps.user.userMeasurements && nextProps.user.userMeasurements[0]) {
      this.makeUserState(nextProps.user);
    }
  }


  onSaveMeasurements(ev) {
    if (ev) ev.preventDefault();
    const lifestyle = invertLifeStyle(this.state);
    const toSend = Object.assign(this.state, { lifestyle });
    delete toSend.id;
    this.props.saveMeasurements(toSend, this.props.user);
  }

  change(ev, param) {
    const obj = {};
    obj[param] = ev.target.value;

    this.setState(obj);
  }

  updateLifestyle(ev) {
    this.setState({ lifestyle: ev.target.value }, this.calculateRecommendation);
  }

  updateBMR(ev) {
    this.setState({ BMR: ev.target.value }, this.calculateRecommendation);
  }

  calculateRecommendation() {
    const calories = Math.round(this.state.BMR * this.state.lifestyle);
    const protein = Math.round((calories * 0.3) / 4);
    const carbs = Math.round((calories * 0.4) / 4);
    const fat = Math.round((calories * 0.3) / 9);
    this.setState({ recommendation: {
      calories,
      protein,
      carbs,
      fat
    } });
    this.props.bmrCalories(calories);
  }

  makeUserState(nextUser) {
    let { user } = this.props;
    if (nextUser) {
      user = nextUser;
    }
    const measurements = user.userMeasurements;
    const lifestyle = lifeStyle(user);
    this.setState(Object.assign(this.state,
      measurements[measurements.length - 1],
      {
        age: user.birthdate ? user.birthdate.slice(0, 10) : null,
        lifestyle
      }
    ), this.calculateBMR);
  }

  calculateBMR(ev) {
    const age = Math.floor((new Date() - new Date(this.state.age)) / 86400000 / 364);
    if (ev) ev.preventDefault();
    const gender = this.state.gender === 'MALE' ? 5 : -161;
    const height = this.state.units === 'imperial' ? (2.54 * this.state.height) / 100 : this.state.height / 100;
    const weight = this.state.units === 'imperial' ? this.state.weight * 0.45359237 : this.state.weight;
    this.setState({ BMR: Math.round(((10 * weight) + (625 * height) + gender) - (5 * age)) }, this.calculateRecommendation);
  }

  render() {
    const { recommendation } = this.state;
    return (
      <div>
        <MeasurementsForm
          age={this.state.age}
          btnTxt={this.props.saveMeas ? 'Save' : 'Calculate'}
          gender={this.state.gender}
          height={this.state.height}
          submitForm={this.props.saveMeas ? this.onSaveMeasurements : this.calculateBMR}
          units={this.state.units}
          weight={this.state.weight}
          change={this.change}
        />
        {
          this.props.saveMeas ? '' : (
            <div>
              <div className="form-group">
                <label htmlFor="bmr">BMR:</label>
                <Input
                  type="number"
                  id="bmr"
                  className="form-control"
                  value={this.state.BMR}
                  onChange={this.updateBMR}
                />
                <label htmlFor="lifestyle">Lifestyle:</label>
                <Select
                  id="lifestyle"
                  className="form-control"
                  value={this.state.lifestyle}
                  onChange={this.updateLifestyle}
                >
                  <option value={1.2}>Sedentary</option>
                  <option value={1.375}>Normal</option>
                  <option value={1.55}>Active</option>
                </Select>
              </div>
              {recommendation.calories ? (
                <div className="alert alert-info">
                  <h4>Daily Maintenance Values</h4>
                  <b>Calories:</b> {recommendation.calories}<br />
                  <b>Protein:</b> {recommendation.protein}g<br />
                  <b>Carbs:</b> {recommendation.carbs}g<br />
                  <b>Fat:</b> {recommendation.fat}g<br />
                </div>
                ) : null}
            </div>
          )
        }
      </div>
    );
  }
}

BMRCalculator.propTypes = {
  saveMeasurements: PropTypes.func,
  user: PropTypes.object,
  bmrCalories: PropTypes.func,
  saveMeas: PropTypes.func
};

const mapStateToProps = ({ root }) => ({
  user: root.user
});

const mapDispatchToProps = dispatch => ({
  saveMeasurements: (measurements, user) => dispatch(saveMeasurements(measurements, user)),
  bmrCalories: calories => dispatch(bmrCalories(calories))
});

export default connect(mapStateToProps, mapDispatchToProps)(BMRCalculator);



