import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import ReactHighcharts from 'react-highcharts';
import { destroyMeasurement } from './dispatch';
import UpdateWeight from '../BMRCalculator/UpdateWeight';
import linedata from './_linedata';


class WeightChart extends React.Component {
  constructor(props) {
    super();

    this.state = { display: 'chart' };
    this.changeDisplay = this.changeDisplay.bind(this);
    this.chartSeries = [{
      field: 'weight',
      name: 'Weight',
      color: '#ff7f0e',
      style: {
        strokeWidth: 2,
      }
    }];
    this.linedata = linedata(props.user.userMeasurements, props.user.programs);
  }

  changeDisplay(display) {
    this.setState({ display });
  }

  render() {
    const { user } = this.props;
    return (
      <div>
        <div>
          <ul className="nav nav-tabs">
            <li
              className={this.state.display === 'chart' ? 'active' : null}
              onClick={() => this.changeDisplay('chart')}
            >
              <a>Chart</a>
            </li>
            <li
              className={this.state.display === 'table' ? 'active' : null}
              onClick={() => this.changeDisplay('table')}
            >
              <a>Table</a>
            </li>
          </ul>
        </div>
        <UpdateWeight />
        {
          this.state.display === 'chart'
          ? <ReactHighcharts config={linedata(user.userMeasurements, user.programs)} />
          : <div style={{ marginTop: '15px' }}>
            {
              user.userMeasurements.sort((aa, bb) => {
                if (new Date(aa.createdAt) > new Date(bb.createdAt)) return 1;
                if (new Date(aa.createdAt) < new Date(bb.createdAt)) return -1;
                return 0;
              }).map(meas => (
                <p key={`${meas.id}_${meas.createdAt}_${meas.weight}`}>
                  <button
                    className="btn btn-default btn-xs"
                    onClick={() => this.props.destroyMeasurement(meas.id)}
                  >
                    <span className="glyphicon glyphicon-remove text-danger" />
                  </button>
                  {' '}
                  {meas.weight} {meas.units === 'imperial' ? 'lbs' : 'kg'}
                  <span className="pull-right">
                    {new Date(meas.createdAt).toDateString()}
                  </span>
                </p>
              )
            )}
          </div>
        }
      </div>
    );
  }
}

WeightChart.propTypes = {
  user: PropTypes.object,
  destroyMeasurement: PropTypes.func
};

const mapStateToProps = ({ root }) => ({
  user: root.user
});

const mapDispatchToProps = dispatch => ({
  destroyMeasurement: id => dispatch(destroyMeasurement(id))
});

export default connect(mapStateToProps, mapDispatchToProps)(WeightChart);



