import React from 'react';
import PropTypes from 'prop-types';

import { connect } from 'react-redux';
import { Link } from 'react-router';
import { Input, Label } from 'components/FormComponents';
import Modal from 'components/ModalCoverage';
import styled from 'styled-components';
import Button from 'components/Button';
import ListGroup from 'components/ListGroup';
import media from 'theme/media';

import { fetchFoods } from '../dispatch';
import SelectFood from '../SelectFood';

const Wrapper = styled.div`
  max-width: 800px;
  padding: 1.5rem;
  ${media.mdUp`
    min-width: 800px;
  `}
`;

class SelectFoodContainer extends React.Component {
  constructor() {
    super();
    this.state = { searchVal: '' };
    this.handleChangeSearchVal = this.handleChangeSearchVal.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChangeSearchVal(ev) {
    this.setState({ searchVal: ev.target.value });
  }

  handleSubmit(ev) {
    ev.preventDefault();
    this.props.fetchFoods(this.state.searchVal);
  }

  render() {
    const { addMeal, day, searchResults, display, hideModal } = this.props;
    return (
      <Modal display={display} overlayClick={hideModal}>
        <Wrapper>
          <h4 className="modal-title">
            { day.toDateString() }
          </h4>
          <h4 className="modal-title">
            { addMeal.name }
          </h4>
          <form className="form-group" onSubmit={this.handleSubmit}>
            <Label htmlFor="searchVal">Search Terms:</Label>
            <div className="input-group">
              <Input
                id="searchVal"
                value={this.state.searchVal}
                onChange={this.handleChangeSearchVal}
              />
              <Button primary>Submit</Button>
            </div>
          </form>
          <ListGroup>
            {searchResults.map(food => <SelectFood food={food} key={food.id} />)}
            <Link to="/database">Add to Database</Link>
          </ListGroup>
        </Wrapper>
      </Modal>
    );
  }
}

SelectFoodContainer.propTypes = {
  fetchFoods: PropTypes.func,
  hideModal: PropTypes.func,
  display: PropTypes.bool,
  day: PropTypes.object,
  addMeal: PropTypes.object,
  searchResults: PropTypes.array,
};

const mapStateToProps = ({ root, foodrecord }) => ({
  day: root.date,
  addMeal: foodrecord.addMeal,
  display: foodrecord.displayModal,
  searchResults: foodrecord.foodSearchResults
});

const mapDispatchToProps = dispatch => ({
  fetchFoods: val => dispatch(fetchFoods(val)),
  hideModal: () => dispatch({ type: 'HIDE_MODAL' })
});

export default connect(mapStateToProps, mapDispatchToProps)(SelectFoodContainer);



