import React from 'react';
import PropTypes from 'prop-types';
import { Input, Label } from 'components/FormComponents';
import styled from 'styled-components';

const Wrapper = styled.div`
  flex-grow: 1;
  padding-left: 7px;
  padding-right: 7px;
  &:first-child {
    padding-left: 0px;
  }
  &:last-child {
    padding-right: 0px;
  }
`;

const Macronutrient = ({ label, change, macro, param }) => (
  <Wrapper>
    <Label htmlFor={`macro${label}`}>{label}:</Label>
    <Input
      id={`macro${label}`}
      type="number"
      min={0}
      max={3000}
      initialValue={''}
      value={macro}
      onChange={ev => change(ev, param)}
      success={macro >= 0}
      danger={macro < 0}
    />
  </Wrapper>
);

Macronutrient.propTypes = {
  label: PropTypes.string,
  change: PropTypes.func,
  macro: PropTypes.number,
  param: PropTypes.string
};

export default Macronutrient;



