import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ListGroup from 'components/ListGroup';
import Button from 'components/Button';
import Badge from 'components/Badge';
import Well from 'components/Well';

const FoodsList = ({ searchedFoods, selectFood }) => {
  if (!searchedFoods.length) {
    return null;
  }

  return (
    <Well>
      <ListGroup>
        { searchedFoods.map(fd => (
          <Button onClick={() => selectFood(fd)} key={fd.id}>
            {fd.longname}
            <Badge>{fd.maxMacro}</Badge>
          </Button>
        ))}
      </ListGroup>
    </Well>
  );
};

FoodsList.propTypes = {
  searchedFoods: PropTypes.array,
  selectFood: PropTypes.func
};

const mapStateToProps = ({ mealplanner }) => ({
  searchedFoods: mealplanner.searchedFoods,
});

export default connect(mapStateToProps)(FoodsList);



