import React, { Component } from 'react';
import LoginForm from 'containers/Login/containers/LoginForm';
import SignupForm from 'containers/Login/containers/SignupForm';
import Modal from 'components/ModalCoverage';

class Nav extends Component {
  constructor() {
    super();
    this.state = {
      displayLogin: false,
      displaySignup: false
    };
    this.showLogin = this.showLogin.bind(this);
    this.showSignup = this.showSignup.bind(this);
  }

  showLogin() {
    this.setState({ displayLogin: true, displaySignup: false });
  }

  showSignup() {
    this.setState({ displaySignup: true, displayLogin: false });
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-default navbar-fixed-top">
          <div className="container-fluid">
            <div className="navbar-header">
              <a className="navbar-brand">KnowYourMacros</a>
            </div>
            <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
              <span className="icon-bar" />
              <span className="icon-bar" />
              <span className="icon-bar" />
            </button>
            <div className="collapse navbar-collapse" id="myNavbar">
              <ul className="nav navbar-nav navbar-right">
                <li>
                  <a className="logInClick" onClick={this.showSignup}>
                    Sign Up
                    <Modal display={this.state.displaySignup}>
                      <SignupForm />
                    </Modal>
                  </a>
                </li>
                <li>
                  <a className="logInClick" onClick={this.showLogin}>
                    Log In
                    <Modal display={this.state.displayLogin}>
                      <LoginForm />
                    </Modal>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <div id="mainImgDiv" style={{ height: '500px' }}>
          <div id="announcementDiv">
            <h2 id="announcement">You Set Your Goal - We Set Your Diet</h2>
            <h2 id="mainMessage">Resources to Improve your Nutrition</h2>
            <h2 id="mainMessage">Reach Macronutrients Goals to the Exact Gram</h2>
          </div>
        </div>

      </div>
    );
  }
}

export default Nav;




