import styled from 'styled-components';
import media from 'theme/media';

export default styled.div`
  padding: 0rem 0.7rem;
  margin: auto;

  ${media.mdUp`
    max-width: 1050px
    ${props => props.fluid && `
      width: 100% !important;
    `}
  `}
  
`;



