import api from 'utils/api';
import { RECEIVE_GOALS } from 'app/redux/constants';

/**
 * Save the user's goals to the database
 * @param {object} param0
 * @param {[{pGoal: number, cGoal: number, fGoal: number}]} param0.trainingGoals
 * @param {[{pGoal: number, cGoal: number, fGoal: number}]} param0.restingGoals
 */
export const saveGoals = ({ trainingGoals, restingGoals }) => (dispatch) => {
  let train = trainingGoals.reduce((memo, meal) => {
    const mm = { ...memo };
    mm[meal.id] = {
      protein: meal.pGoal,
      carbs: meal.cGoal,
      fat: meal.fGoal
    };
    return mm;
  }, {});

  let rest = restingGoals.reduce((memo, meal) => {
    const mm = { ...memo };
    mm[meal.id] = {
      protein: meal.pGoal,
      carbs: meal.cGoal,
      fat: meal.fGoal
    };
    return mm;
  }, {});

  const goalTemplate = { protein: 0, carbs: 0, fat: 0 };

  for (let i = 0; i < 6; i++) {
    if (!train[i]) {
      train[i] = { ...goalTemplate };
    }
    if (!rest[i]) {
      rest[i] = { ...goalTemplate };
    }
  }

  train = Object.values(train);
  rest = Object.values(rest);

  return api.post('/api/goals/meals', { train, rest })
    .then(({ data }) => dispatch({ type: RECEIVE_GOALS, payload: data }));
};



