import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Panel from 'components/Panel';
import Container from 'components/Container';
import Button from 'components/Button';
import { Input } from 'components/FormComponents';
import { retainFood } from 'containers/MealPlanner/dispatch';
import { getMeals } from './dispatch';

class PublicMeals extends React.Component {
  constructor(props) {
    super();
    this.state = {
      keyword: '',
      meals: [],
      postWorkout: false
    };
    props.getMeals(this.state);
    this.onUpdateKeyWord = this.onUpdateKeyWord.bind(this);
    this.searchPublicMeals = this.searchPublicMeals.bind(this);
    this.changePostWorkout = this.changePostWorkout.bind(this);
    this.useThisMeal = this.useThisMeal.bind(this);
  }

  onUpdateKeyWord(ev) {
    this.setState({ keyword: ev.target.value });
  }

  onUpdateMeal(mealId) {
    if (this.state.meals.indexOf(mealId) > -1) {
      this.setState({
        meals: this
          .state
          .meals
          .filter(meal => meal !== mealId)
      });
    } else {
      const meals = this
        .state
        .meals
        .slice();
      meals.push(mealId);
      this.setState({ meals });
    }
  }

  searchPublicMeals(ev) {
    ev.preventDefault();
    this
      .props
      .getMeals(this.state);
  }

  changePostWorkout() {
    this.setState({
      postWorkout: !this.state.postWorkout
    });
  }

  useThisMeal(ix) {
    const meal = this.props.meals[ix];
    meal
      .foodRecords
      .forEach((record) => {
        this.props.retainFood(record.abbrev);
      });
  }

  render() {
    const mealNames = [
      'Breakfast',
      'Morning Snack',
      'Lunch',
      'Afternoon Snack',
      'Dinner',
      'Evening Snack'
    ];
    return (
      <Container>
        <div className="text-center">
          <h3>Public Meals</h3>
          <h4>Meals recently published by other members</h4>
        </div>
        <form onSubmit={this.searchPublicMeals}>
          <div className="form-group">
            <input
              type="text"
              placeholder="Meals With Foods Containing This Phrase"
              className="form-control"
              value={this.state.keyword}
              onChange={this.onUpdateKeyWord}
            />
          </div>
          <div className="row form-group">
            {mealNames.map((meal, ix) => (
              <div className="col-md-2 text-center" key={ix}>
                <label htmlFor="meal">
                  <Input
                    id="meal"
                    type="checkbox"
                    className="btn btn-default"
                    selected={this.state.meals.indexOf(ix + 1) > -1}
                    onChange={() => this.onUpdateMeal(ix + 1)}
                  />
                  <br /> {mealNames[ix]}
                </label>
              </div>
            ))}
          </div>
          <div className="form-group">
            <Button primary>Filter</Button>
          </div>
          <label htmlFor="postWorkout">
            <Input
              id="postWorkout"
              type="checkbox"
              selected={this.state.postWorkout}
              onChange={this.changePostWorkout}
            />
            Post Workout
          </label>
        </form>
        <div className="row">
          {this.props.meals.map((meal, ix) => (
            <div
              key={meal.id}
              className="col-md-4"
              style={{
                height: '250px',
                overflowY: 'hidden'
              }}
            >
              <Panel type="info" title={mealNames[meal.meal - 1]}>
                <Button xs onClick={() => this.useThisMeal(ix)}>
                  <span className="glyphicon glyphicon-thumbs-up" />
                </Button>
                {meal
                  .foodRecords
                  .map(record => (
                    <h5 key={record.id}>
                      {record.abbrev.longname}
                    </h5>
                  ))}
              </Panel>
            </div>
          ))}
        </div>
      </Container>
    );
  }
}

PublicMeals.propTypes = {
  getMeals: PropTypes.func,
  retainFood: PropTypes.func,
  meals: PropTypes.array
};

const mapStateToProps = ({ meals }) => ({ meals: meals.meals });

const mapDispatchToProps = dispatch => ({
  getMeals: filter => dispatch(getMeals(filter)),
  retainFood: food => dispatch(retainFood(food))
});

export default connect(mapStateToProps, mapDispatchToProps)(PublicMeals);



