import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Container from 'components/Container';
import Well from 'components/Well';

import debounce from 'utils/debounce';

import { searchFood, retainFood, removeFood, calculateFood, checkPossibility, addToSearchList } from './dispatch';
import ReportTable from './components/ReportTable';
import FoodsList from './components/FoodsList';
import RetainedFoodsList from './components/RetainedFoodsList';
import GoalForm from './containers/GoalForm';
import SearchForm from './containers/SearchForm';

class MealPlanner extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      retainedFoods: [],
      searchVal: '',
      proteinGoal: 20,
      carbGoal: 30,
      fatGoal: 10,
      err: null
    };

    this.onSearchSubmit = this.onSearchSubmit.bind(this);
    this.changeSearchTerm = this.changeSearchTerm.bind(this);
    this.calculateFood = this.calculateFood.bind(this);
    this.updateGoal = this.updateGoal.bind(this);
    this.selectFood = this.selectFood.bind(this);
    this.removeFood = this.removeFood.bind(this);

    this.submit = debounce(this.onSearchSubmit, 300);
  }

  onSearchSubmit(ev) {
    if (ev) {
      ev.preventDefault();
    }
    if (this.state.searchVal === this.props.lastSearch) {
      return null;
    }
    if (this.state.searchVal.length > 2) {
      this.props.searchFood(this.state.searchVal);
    }
    return Promise.resolve();
  }

  changeSearchTerm(ev) {
    this.setState({ searchVal: ev.target.value }, this.submit);
  }

  removeFood(food) {
    this.props.removeFood(food)
      .then(() => this.props.addToSearchList(food))
      .then(() => {
        if (this.state.retainedFoods.length > 3) {
          checkPossibility({
            foods: this.state.retainedFoods,
            goals: this.state
          });
        }
      });
  }

  updateGoal(ev, goal) {
    const myGoal = {};
    myGoal[goal] = ev.target.value;
    this.setState(myGoal, check);

    function check() {
      this.setState({ err: null }, () => {
        if (this.props.retainedFoods.length > 3) {
          checkPossibility({
            foods: this.props.retainedFoods,
            goals: this.state
          })
        .catch(err => this.setState({ err }));
        }
      });
    }
  }

  selectFood(fd) {
    const { retainedFoods } = this.props;
    if (retainedFoods.filter(rfd => rfd.id === fd.id).length) {
      return;
    }
    this.props.retainFood(fd)
      .then(() => {
        if (this.state.retainedFoods.length > 3) {
          checkPossibility({
            foods: this.state.retainedFoods,
            goals: this.state
          });
        }
      });
  }

  calculateFood() {
    const { retainedFoods } = this.props;
    this
      .props
      .calculateFood({
        id: retainedFoods.map(food => food.id),
        proteinGoal: this.state.proteinGoal,
        carbGoal: this.state.carbGoal,
        fatGoal: this.state.fatGoal
      })
      .catch(err => this.setState({ err }));
  }

  render() {
    const { retainedFoods } = this.props;
    return (
      <Container>
        <h3>Meal Planner</h3>
        <hr />
        <ReportTable error={this.state.err} /> {retainedFoods.length >= 3 && (
          <GoalForm
            proteinGoal={this.state.proteinGoal}
            carbGoal={this.state.carbGoal * 1}
            fatGoal={this.state.fatGoal * 1}
            calculateFood={this.calculateFood}
            updateGoal={this.updateGoal}
          />
        )}
        <RetainedFoodsList removeFood={this.removeFood} />
        <Well>
          <SearchForm
            onSearchSubmit={this.onSearchSubmit}
            searchVal={this.state.searchVal}
            changeSearchTerm={this.changeSearchTerm}
          />
        </Well>
        <FoodsList selectFood={this.selectFood} />
      </Container>
    );
  }
}

MealPlanner.propTypes = {
  searchFood: PropTypes.func,
  addToSearchList: PropTypes.func,
  retainFood: PropTypes.func,
  removeFood: PropTypes.func,
  calculateFood: PropTypes.func,
  retainedFoods: PropTypes.array,
  lastSearch: PropTypes.string
};

const mapStateToProps = ({ mealplanner, root }) => ({
  lastSearch: mealplanner.lastSearch,
  retainedFoods: mealplanner.retainedFoods,
  date: root.date
});

const mapDispatchToProps = dispatch => ({
  addToSearchList: food => dispatch(addToSearchList(food)),
  searchFood: searchTerm => dispatch(searchFood(searchTerm)),
  retainFood: food => dispatch(retainFood(food)),
  removeFood: id => dispatch(removeFood(id)),
  calculateFood: params => dispatch(calculateFood(params)),
});

export default connect(mapStateToProps, mapDispatchToProps)(MealPlanner);



