import React from 'react';
import PropTypes from 'prop-types';
// import FeedbackInput from 'components/FeedbackInput';
import { Input, Label } from 'components/FormComponents';

const ItemDescription = ({ foodName, onchange, nameError }) => (
  <div>
    <Label htmlFor="foodname">Item Description:</Label>
    <Input
      id="foodname"
      placeholder="Let the first word be a very general description"
      value={foodName}
      onChange={onchange}
      success={!nameError.length && foodName.length > 0}
      warning={nameError.length > 0 && foodName.length > 0}
    />
    <div className="help-block">
      Use commas (, ) to separate food specifics. {' '}
      Avoid using quotation marks (' or ") as they will be removed.
    </div>
    <div className="text-warning">
      {nameError}
    </div>
  </div>
);

ItemDescription.propTypes = {
  foodName: PropTypes.string,
  onchange: PropTypes.func,
  nameError: PropTypes.string
};

export default ItemDescription;



