import React from 'react';
import PropTypes from 'prop-types';

import { connect } from 'react-redux';
import { Select, Input } from 'components/FormComponents';
import Button from 'components/Button';
import { handleAddFoodRecord } from './dispatch';

class SelectFood extends React.Component {
  constructor() {
    super();
    this.state = {
      weight: 1,
      amount: ''
    };

    this.onAmountChange = this.onAmountChange.bind(this);
    this.onWeightChange = this.onWeightChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onAmountChange(ev) {
    this.setState({ amount: ev.target.value });
  }

  onWeightChange(ev) {
    this.setState({ weight: ev.target.value });
  }

  onSubmit(ev) {
    ev.preventDefault();
    if (this.state.amount <= 0) return;
    this.props.handleAddFoodRecord({
      abbrev_id: this.props.food.id,
      date: this.props.day.toDateString(),
      meal: this.props.addMeal.id,
      quantity: this.state.amount,
      unit: this.state.weight * 1,
    }, this.props.day);
  }

  render() {
    const { food } = this.props;
    return (
      <div className="clearfix">
        <form onSubmit={this.onSubmit}>
          <b>{ food.Main }</b> - { food.Sub }
          <br />
          <div className="form-inline">
            <Input
              sm
              inline
              value={this.state.amount}
              onChange={this.onAmountChange}
              type="number"
              min="0"
              max="500"
              step="0.01"
            />
            { ' ' }
            <Select
              sm
              inline
              value={this.state.weight}
              onChange={this.onWeightChange}
            >
              { food.weights.map(weight => (
                <option key={weight.Seq} value={weight.Seq}>
                  { weight.normalized.txt }
                </option>)) }
            </Select>
            { ' ' }
            <Button sm disabled={this.state.amount <= 0}>
              Submit
            </Button>
          </div>
        </form>
        <Button xs>
          Favorite
        </Button>
      </div>
    );
  }
}

SelectFood.propTypes = {
  handleAddFoodRecord: PropTypes.func,
  food: PropTypes.object,
  day: PropTypes.object,
  addMeal: PropTypes.object
};

const mapStateToProps = state => ({
  day: state.root.date,
  addMeal: state.foodrecord.addMeal,
  searchResults: state.foodrecord.foodSearchResults
});

const mapDispatchToProps = dispatch => ({
  handleAddFoodRecord: (record, date) => dispatch(handleAddFoodRecord(record, date))
});

export default connect(mapStateToProps, mapDispatchToProps)(SelectFood);



