import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { calculateDayMacros } from 'containers/FoodRecord/dispatch';
import { Pie } from 'react-chartjs-2';
import { Left, Right, Wrapper } from './styled';

const pieOptions = {
  legend: { display: false }
};

const Row = ({ children, number }) => (
  <div>
    <b>{children}:</b> {number}g
  </div>
);

Row.propTypes = {
  children: PropTypes.node,
  number: PropTypes.number
};

const DayMacroSummary = ({ foodrecord }) => {
  const { raw, percentCals } = calculateDayMacros(foodrecord);

  const piedata = {
    labels: ['Fat', 'Protein', 'Carbs'],
    datasets: [
      {
        data: [percentCals.fat, percentCals.protein, percentCals.carbs],
        backgroundColor: ['rgba(255, 99, 132, 0.3)', 'rgba(54, 162, 235, 0.3)', 'rgba(255, 206, 86, 0.3)'],
        hoverBackgroundColor: ['rgba(255, 99, 132, 0.6)', 'rgba(54, 162, 235, 0.6)', 'rgba(255, 206, 86, 0.6)'],
        borderColor: ['rgba(255,99,132,1)', 'rgba(54, 162, 235, 1)', 'rgba(255, 206, 86, 1)'],
        borderWidth: '1px'
      }
    ]
  };


  return (
    <Wrapper>
      <Left>
        <Row number={raw.calories}>Calories</Row>
        <Row number={raw.protein}>Protein</Row>
        <Row number={raw.carbs}>Carbs</Row>
        <Row number={raw.fat}>Fat</Row>
      </Left>
      <Right>
        {raw.calories
          ? 'Percent Calories:'
          : null}
        {percentCals.calories
          ? (
            <div style={{ maxWidth: '120px', margin: 'auto' }}>
              <Pie
                data={piedata}
                width={120}
                height={120}
                options={pieOptions}
              />
            </div>
          )
          : null}
      </Right>
    </Wrapper>
  );
};

DayMacroSummary.propTypes = {
  foodrecord: PropTypes.array
};

const mapStateToProps = ({ foodrecord }) => ({ foodrecord: foodrecord.record });

export default connect(mapStateToProps)(DayMacroSummary);



