import api from 'utils/api';
import { modWeight } from 'containers/ShoppingList/dispatch';
import foodRecordCache from 'utils/foodRecordCache';
import { ADD_MEAL, RECEIVE_CALORIES, RECEIVE_FOOD_SEARCH_RESULT, RECEIVE_FOOD_RECORD, UPDATE_RECORD, REMOVE_FOOD_RECORD_ITEM, CONFIRM_RECORD, UPDATE_RECORD_QUANTITY } from '../constants';

const initialState = {
  addMeal: { name: 'Breakfast' },
  calories: 0,
  displayModal: false,
  foodSearchResults: [],
  record: []
};

export default (state = initialState, action) => {
  let newState;
  switch (action.type) {
    case ADD_MEAL:
      return { ...state, addMeal: action.payload };

    case RECEIVE_FOOD_SEARCH_RESULT:
      return { ...state, foodSearchResults: action.payload };

    case RECEIVE_CALORIES:
      return { ...state, calories: action.payload };

    case 'DISPLAY_MODAL':
      return { ...state, displayModal: true };

    case 'HIDE_MODAL':
      return { ...state, displayModal: false };

    case REMOVE_FOOD_RECORD_ITEM:
      foodRecordCache.removeRecord(action.payload, action.date);
      return { ...state, record: state.record.filter(food => food.id !== action.payload) };

    case RECEIVE_FOOD_RECORD:
      if (Array.isArray(action.payload)) { // records for an entire day
        newState = { ...state, record: action.payload };
        foodRecordCache[action.date] = newState.record.slice();
      } else { // single item
        foodRecordCache.addRecord(action.payload);
        newState = { ...state, record: state.record.slice() };
        if (new Date(action.payload.Date).toDateString() === action.date.toDateString()) {
          newState.record.push(action.payload);
        }
      }
      return newState;

    case UPDATE_RECORD:
      return {
        ...state,
        record: state.record.map((food) => {
          const fd = { ...food };
          if (food.meal.id === action.payload.id) {
            fd.meal = action.payload;
          }
          return fd;
        })
      };

    case UPDATE_RECORD_QUANTITY:
      foodRecordCache.updateRecord(action.payload);
      return {
        ...state,
        record: state.record.map((food) => {
          let fd = { ...food };
          if (food.id === action.payload.id) {
            fd = action.payload;
          }
          return fd;
        })
      };

    case CONFIRM_RECORD:
      newState = {
        ...state,
        record: state.record.slice().map((fd) => {
          const food = { ...fd };
          if (fd.id === action.payload) {
            food.confirmed = true;
          }
          return food;
        })
      };
      foodRecordCache[state.date.toDateString().slice(4)] = newState.foodrecord.slice();
      return newState;

    default:
      return state;
  }
};

/** Add records to the database
 * @param {integer} meal
 * @param {integer} mealId
 * @param {string} date
 */
export const handleAddMeal = (meal, mealId, date) => (dispatch) => {
  meal.forEach((factor) => {
    factor.foods.forEach((food) => {
      const quantities = modWeight(food, factor.weight);
      api.post('/api/food-record', {

        abbrev_id: food.id,
        date: date.toDateString(),
        meal: mealId,
        quantity: quantities[0],
        unit: food.weights[0].Seq,
        confirmed: false
      })
      .then(({ data }) => {
        dispatch({
          type: RECEIVE_FOOD_RECORD,
          payload: data,
          date
        });
      });
    });
  });
};

  // api
  //   .post('/api/food-record', record, {
  //     params: {
  //       token: getToken()
  //     }
  //   })

/** Update the database to make a meal public
 * @param {number} mealId - Meal id (an integer)
 */
export const makeMealPublic = mealId => dispatch => api
  .put('/api/food-record/meal', { mealId })
  .then(({ data }) => dispatch({ type: UPDATE_RECORD, payload: data }));




