import api from 'utils/api';
import { RECEIVE_MEALS } from 'app/redux/constants';

export const getMeals = ({ keyword, meals, postWorkout }) => dispatch => api
.get('/api/meal', { params: { keyword, meals, postWorkout } })
.then(({ data }) => dispatch({ type: RECEIVE_MEALS, payload: data }));



