export const lifeStyle = (user) => {
  let lifestyle;
  switch (user.lifestyle) {
    case 'Normal':
      lifestyle = 1.375;
      break;
    case 'Active':
      lifestyle = 1.55;
      break;
    default:
      lifestyle = 1.2;
      break;
  }
  return lifestyle;
};

export const invertLifeStyle = (state) => {
  let lifestyle;
  switch (state.lifestyle) {
    case 1.375:
      lifestyle = 'Normal';
      break;
    case 1.55:
      lifestyle = 'Active';
      break;
    default:
      lifestyle = 'Sedentary';
      break;
  }

  return lifestyle;
};



