import React from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import PropTypes from 'prop-types';

import Button from 'components/Button';
import Well from 'components/Well';
import Container from 'components/Container';
import { Input, Select } from 'components/FormComponents';
import OptionGroup from 'components/OptionGroup';
import BMRCalculator from '../BMRCalculator';
import { saveGoals } from './dispatch';
import GoalsReport from './components/GoalsReport';
import { getGoals } from './utils';

class SetGoals extends React.Component {
  constructor() {
    super();
    this.state = {
      goals: {
        pGoal: 150,
        cGoal: 150,
        fGoal: 72
      },
      numMeals: 4,
      schedule: [1, 1, 'Workout', 1, 1],
      workoutId: 2,
      beforeAfter: 'after',
      maintenanceCal: 0,
      goal: 'Lose Fat',
      trainingGoals: null,
      restingGoals: null,
      bmrCals: 0
    };

    this.changeSchedule = this.changeSchedule.bind(this);
    this.changeNumberMeals = this.changeNumberMeals.bind(this);
    this.changeBeforeAfter = this.changeBeforeAfter.bind(this);
    this.changeWorkoutId = this.changeWorkoutId.bind(this);
    this.calculateGoals = this.calculateGoals.bind(this);
    this.changeMaintenanceCal = this.changeMaintenanceCal.bind(this);
    this.changeGoal = this.changeGoal.bind(this);
    this.onSaveGoals = this.onSaveGoals.bind(this);
  }

  onSaveGoals() {
    this.props.saveGoals(this.state)
      .then(() => browserHistory.push('/shopping-list'));
  }

  setGoals({ maintenanceCal, goal, weight, units }) {
    let pGoal;
    let cal;
    const wght = units === 'metric' ? weight * 2.2 : weight;
    if (goal === 'Build Muscle') {
      pGoal = wght * 1.2;
      cal = maintenanceCal + 300;
    } else if (goal === 'Maintain') {
      pGoal = wght * 1;
      cal = maintenanceCal;
    } else {
      pGoal = wght * 0.8;
      cal = maintenanceCal - 500;
    }
    const fGoal = Math.round(((cal * 0.32) * 10) / 9) / 10;

    const cGoal = Math.round(((cal - (fGoal * 9) - (pGoal * 4)) * 10) / 4) / 10;
    this.setState({
      maintenanceCal: cal,
      goals: {
        pGoal,
        cGoal,
        fGoal
      }
    });
    return { cal, pGoal, cGoal, fGoal };
  }

  changeSchedule(mealId) {
    if (this.state.schedule.indexOf(mealId) > -1) {
      this.setState({ schedule: this.state.schedule.filter(item => item !== mealId) });
    } else {
      let schedule = this.state.schedule.slice();
      schedule.push(mealId);
      schedule = schedule.sort();
      this.setState({ schedule });
    }
  }

  changeNumberMeals(ev) {
    this.setState({ numMeals: ev.target.value }, () => this.changeWorkoutId({
      target: {
        value: this.state.workoutId
      }
    }));
  }

  changeBeforeAfter(ev) {
    this.setState({ beforeAfter: ev.target.value });
  }

  changeWorkoutId(ev) {
    const schedule = [];
    for (let i = 0; i < this.state.numMeals; i++) {
      schedule[i] = 1;
    }
    if (this.state.beforeAfter === 'before') {
      schedule.splice(ev.target.value * 1, 0, 'Workout');
    } else {
      schedule.splice((ev.target.value * 1) + 1, 0, 'Workout');
    }
    this.setState({ schedule });
  }

  changeMaintenanceCal(ev) {
    this.setState({ bmrCals: this.props.bmrCalories });
    this.setState({ maintenanceCal: ev.target.value });
  }

  changeGoal(ev) {
    this.setState({ goal: ev.target.value });
  }

  calculateGoals(maintenanceCal, schedule, goal) {
    const mCal = maintenanceCal || this.props.bmrCalories;

    const pGoal = Math.round((mCal * 3) / 4) / 10;
    const cGoal = Math.round((mCal * 4) / 4) / 10;
    const fGoal = Math.round((mCal * 3) / 9) / 10;

    this.setState({
      trainingGoals: getGoals(schedule, {
        pGoal,
        cGoal,
        fGoal
      }, true, goal, cGoal, pGoal),
      restingGoals: getGoals(schedule.filter(item => item !== 'Workout'), {
        pGoal,
        cGoal,
        fGoal }, false, goal, cGoal, pGoal)
    });
  }

  render() {
    const mealNames = ['first', 'second', 'third', 'fourth', 'fifth', 'sixth'];
    const meals = [];
    for (let i = 0; i < this.state.numMeals; i++) {
      meals.push(mealNames[i]);
    }
    return (
      <Container>
        <h3>Set Goals</h3>
        <hr />
        <div className="row">
          <div className="col-md-3">
            <Well>
              <BMRCalculator />
            </Well>
          </div>
          <div className="col-md-9">
            <Well small>
              <div className="form-group row">
                <div className="col-md-6">
                  Maintenance Calories:
                  {
                    this.state.maintenanceCal === 0 || (this.state.bmrCals !== this.props.bmrCalories && this.state.bmrCals)
                    ? <Input
                      type="number"
                      value={this.props.bmrCalories}
                      onChange={this.changeMaintenanceCal}
                    />
                    : <Input
                      type="number"
                      value={this.state.maintenanceCal}
                      onChange={this.changeMaintenanceCal}
                    />
                  }
                </div>
                <div className="col-md-6">
                  I want to:
                  <OptionGroup
                    value={this.state.goal}
                    onChange={this.changeGoal}
                    options={['Lose Fat', 'Gain Muscle', 'Maintain']}
                  />
                </div>
              </div>
            </Well>
            <Well>
              I eat
              {' '}
              <Select inline small value={this.state.numMeals} onChange={this.changeNumberMeals}>
                <option value={3}>three</option>
                <option value={4}>four</option>
                <option value={5}>five</option>
                <option value={6}>six</option>
              </Select>
              {' '}
              meals per day
            </Well>
            <Well>
              I workout
              {' '}
              <OptionGroup
                inline
                small
                value={this.state.beforeAfter}
                onChange={this.changeBeforeAfter}
                options={['before', 'after']}
              />
              {' '}
              my
              {' '}
              <Select inline small defaultValue={1} onChange={this.changeWorkoutId}>
                {meals.map((meal, ix) => <option key={meal} value={ix}>{meal}</option>)}
              </Select>
              {' '}
              meal
            </Well>
            <Button
              primary
              onClick={() => this.calculateGoals(this.state.maintenanceCal, this.state.schedule, this.state.goal)}
            >
              Determine Goals
            </Button>
            <GoalsReport goals={this.state.trainingGoals} title="Training Goals" />
            <GoalsReport goals={this.state.restingGoals} title="Resting Goals" />
            {this.state.trainingGoals && this.state.restingGoals && <Button primary onClick={this.onSaveGoals}>Save Meal Goals</Button>}
          </div>
        </div>
      </Container>
    );
  }
}

SetGoals.propTypes = {
  saveGoals: PropTypes.func,
  bmrCalories: PropTypes.string
};

const mapStateToProps = ({ root }) => ({
  bmrCalories: root.bmrCalories
});

const mapDispatchToProps = dispatch => ({
  saveGoals: st => dispatch(saveGoals(st))
});

export default connect(mapStateToProps, mapDispatchToProps)(SetGoals);



