import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import styled from 'styled-components';
import Container from 'components/Container';
import { getList } from './dispatch';
import { makeList, convertWeight } from './list-logic';

const ListGroup = styled.ul.attrs({
  className: 'list-group'
})``;

const ListGroupItem = styled.li.attrs({
  className: 'list-group-item'
})``;

class List extends React.Component {
  constructor(props) {
    super();
    props.getList(new Date());
    this.startDate = new Date().toLocaleDateString();
    this.endDate = new Date(new Date().getTime() + (86400000 * 6)).toLocaleDateString();
  }

  render() {
    const { list } = this.props;
    if (!list) return null;
    const renderList = makeList(list);

    return (
      <Container>
        <h3>Shopping List for { this.startDate } - { this.endDate }</h3>
        <hr />
        {Object.keys(renderList).map(group => (
          <div key={group}>
            <h4>{ group }</h4>
            <ListGroup>
              {
                renderList[group].map(record => (
                  <ListGroupItem key={record.id}>
                    {record.abbrev.longname}
                    <span className="badge badge-default">{convertWeight(record)}</span>
                  </ListGroupItem>))
              }
            </ListGroup>
          </div>))
        }
      </Container>
    );
  }
}

List.propTypes = {
  list: PropTypes.array,
  getList: PropTypes.func
};

const mapStateToProps = ({ shoppinglist }) => ({
  list: shoppinglist.list
});

const mapDispatchToProps = dispatch => ({
  getList: date => dispatch(getList(date))
});

export default connect(mapStateToProps, mapDispatchToProps)(List);



